import * as React from "react";
import Typography from "@material-ui/core/Typography";
import { Card, CardContent, CardActions, IconButton } from "@material-ui/core";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import {
  Tooltip,
  Legend,
  ResponsiveContainer,
  Pie,
  PieChart,
  Cell,
} from "recharts";
import {
  ChartState,
  QueryFilterKey,
  TimePeriod,
  PossibleValues,
  EventField,
  PossibleValueDictionary,
} from "../WidgetEditor/types";
import * as Immutable from "immutable";

type WidgetPreviewPieProps = {
  isEditMode: boolean;
  api: string;
  token: string;
  refreshTime?: Date;

  id: string;
  title?: string;
  description?: string;
  chartState: ChartState;
  filterItems: Immutable.List<QueryFilterKey>;
  period: TimePeriod;
  possibleValues: PossibleValues;
  groupByAvailable: EventField[];

  onEditWidget?: ((id: string) => void) | undefined;
  onDeleteWidget?: ((id: string) => void) | undefined;
};

type WidgetPreviewPieState = {
  rows?: any;
  err?: string;
};

class WidgetPreviewPie extends React.Component<
  WidgetPreviewPieProps,
  WidgetPreviewPieState
> {
  state: WidgetPreviewPieState = {
    rows: undefined,
    err: undefined,
  };

  handleEdit = () => {
    console.log("handleEdit " + this.props.id);
    if (this.props.onEditWidget) {
      this.props.onEditWidget(this.props.id);
    }
  };
  handleDelete = () => {
    console.log("handleDelete " + this.props.id);
    if (this.props.onDeleteWidget) {
      this.props.onDeleteWidget(this.props.id);
    }
  };

  loadData = () => {
    console.log("WidgetPreviewPie loadData");
    this.setState({ rows: undefined, err: undefined });
    //Simple POST request with a JSON body using fetch
    const url1 = this.props.api + "/api/v2/ad-query/aggregation/table";

    const { x, y, op, series } = this.props.chartState;

    const d = {
      filter: {
        keys: this.props.filterItems
          .filter((xx) => xx.type === "set" || xx.type === "dictionary")
          .map((xx) => {
            return {
              field: xx.field,
              op: xx.op,
              value: xx.value,
            };
          }),
        numbers: this.props.filterItems
          .filter((xx) => xx.type === "number")
          .map((xx) => {
            return {
              field: xx.field,
              op: xx.op,
              value: Number.parseFloat(xx.value),
            };
          }),
        lists: [],
        period: this.props.period,
      },
      groupBy: [x, y, series],
      fields: [
        {
          field: y,
          op: op,
        },
      ],
    };
    const requestOptions: RequestInit = {
      method: "POST",
      //mode: "no-cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.props.token,
      },
      body: JSON.stringify(d),
    };
    fetch(url1, requestOptions)
      //.then(r => r.json().then(json => ({ok: r.ok, status: r.status, json})))
      //.then( r => r.ok ? r.json: Promise.reject(r))
      .then((response) => response.json())
      .then((data) => {
        //this.props.onChangeEventFields(data);
        if (data.result == null) {
          throw Error("no rows in result" + JSON.stringify(data));
        } else {
          const r = data.result;
          // .map((item: any)=>{
          //   return item;
          // });
          console.log("WidgetPreviewLineChart r=" + JSON.stringify(r));
          this.setState({ rows: r });
        }
        console.log("WidgetPreviewLineChart data=" + JSON.stringify(data));
      })
      .catch((e) => {
        this.setState({ err: e.message });
      });
  };

  componentDidMount() {
    console.log(
      "WidgetPreviewPie componentDidMount " +
        JSON.stringify(this.props.chartState)
    );
    this.loadData();
  }

  componentDidUpdate(prevProps: WidgetPreviewPieProps) {
    console.log(
      "WidgetPreviewPie componentDidUpdate " +
        JSON.stringify(this.props.chartState)
    );
    if (JSON.stringify(this.props) !== JSON.stringify(prevProps)) {
      // Check if it's a new user, you can also use some unique property, like the ID  (this.props.user.id !== prevProps.user.id)
      this.loadData();
    }
  }

  render() {
    if (this.state.err) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>Error: {this.state.err}</Typography>
          </CardContent>
        </Card>
      );
    }

    if (!this.props.chartState) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>Error: no chartState</Typography>
          </CardContent>
        </Card>
      );
    }

    const { x, y, op, series } = this.props.chartState!;

    if (!x || !y || !op || !series) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>
              Error: chartState content {JSON.stringify(this.props.chartState!)}
            </Typography>
          </CardContent>
        </Card>
      );
    }

    if (!this.state.rows) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>Error: no rows</Typography>
          </CardContent>
        </Card>
      );
    }

    const data = this.state.rows!;

    const seriesField = this.props.groupByAvailable.filter(
      (xx) => xx.name === this.props.chartState.series
    );
    const seriesFieldName: string = this.props.chartState!.series!;
    const seriesFieldType: string = seriesField[0].descriptor.type;
    // const seriesValues: PossibleValueDictionary[] = seriesFieldType == "dictionary"
    //   ? this.props.possibleValues.dictionary.get(seriesFieldName) ?? []
    //   : [];
    // console.log(seriesValues);
    var seriesValues: PossibleValueDictionary[] = [];

    if (seriesFieldType === "set") {
      seriesValues =
        this.props.possibleValues.set.get(seriesFieldName)?.map((xx) => {
          return {
            key: xx,
            value: xx,
          };
        }) || [];
    } else if (seriesFieldType === "dictionary") {
      seriesValues =
        this.props.possibleValues.dictionary.get(seriesFieldName) || [];
    }

    console.log(seriesValues);

    //filter to series in data
    const seriesInDataValues: Set<string> = data.reduce(
      (coll: any, item: any) => {
        const s = item[series];
        coll.add(s);
        return coll;
      },
      new Set()
    );

    seriesValues = seriesValues.filter((sv) => {
      return seriesInDataValues.has(sv.key);
    });

    const v = data.reduce((coll: any, item: any) => {
      //const currentX = item[x];
      const currentX = item[series] + "(" + item[x] + ")";
      const valueForX = coll.get(currentX) || { x: currentX };

      //valueForX[item[series]] = item[op + "_" + y];
      valueForX["value"] = item[op + "_" + y];
      coll.set(currentX, valueForX);

      return coll;
    }, new Map());

    console.log(v);

    const kk = Immutable.List(v.keys()).sortBy((i: any) => i.x);
    const vv = Immutable.List(v.values()).sortBy((i: any) => i.x);

    console.log(kk.toArray());
    console.log(vv.toArray());
    const dd: any[] = vv.toArray();
    //const stateDictionary = ["in", "out"];
    // const dataIn = sampleData.result.filter((x1: any) => x1["event.detector.People.state"] === "in").sort((a,b) => {
    //   let comparison = 0;
    //   if ( a["time.hour"] > b["time.hour"]) {
    //     comparison = 1;
    //   } else if (a["time.hour"] < b["time.hour"]) {
    //     comparison = -1;
    //   }
    //   return comparison;
    // });

    const stringToColour = function (str: string) {
      var hash = 0;
      for (var i = 0; i < str.length; i++) {
        hash = str.charCodeAt(i) + ((hash << 5) - hash);
      }
      var colour = "#";
      for (var ii = 0; ii < 3; ii++) {
        var value = (hash >> (ii * 8)) & 0xff;
        colour += ("00" + value.toString(16)).substr(-2);
      }
      return colour;
    };

    return (
      <Card variant="outlined">
        <CardContent>
          <Typography align="center" color="textSecondary" gutterBottom>
            {this.props.title}
          </Typography>
          <ResponsiveContainer width="95%" height={400}>
            <PieChart
              width={400}
              height={400}
              margin={{ top: 5, right: 20, left: 10, bottom: 5 }}
            >
              <Tooltip />
              <Legend />

              <Pie
                cx="50%"
                cy="50%"
                data={dd}
                label
                innerRadius={80}
                outerRadius={100}
                //stroke={stringToColour(serieVal.key)}
                //fill={stringToColour(serieVal.key)}
                dataKey="value"
                nameKey="x"
                key="value"
              >
                {dd.map((entry, index) => (
                  <Cell
                    key={`cell-${index}`}
                    fill={stringToColour(entry["x"])}
                  />
                ))}
              </Pie>
            </PieChart>
          </ResponsiveContainer>
          <Typography align="center" color="textSecondary">
            {this.props.description}
          </Typography>
        </CardContent>
        {this.props.isEditMode ? (
          <CardActions disableSpacing>
            <IconButton aria-label="edit" onClick={this.handleEdit}>
              <EditIcon />
            </IconButton>
            <IconButton aria-label="delete" onClick={this.handleDelete}>
              <DeleteIcon />
            </IconButton>
          </CardActions>
        ) : null}
        {/* <CardActions>
        <Button size="small">Learn More</Button>
      </CardActions> */}
      </Card>
    );
  }
}

export default WidgetPreviewPie;
