import * as React from "react";
import Chip from "@material-ui/core/Chip";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import FilterListOutlinedIcon from "@material-ui/icons/FilterListOutlined";
//import Paper from '@material-ui/core/Paper';
import {
  EventField,
  QueryFilterKey,
  QueryFilterKeyOperation,
  QueryFilterValueOperation,
  QueryFilterNumberOperation,
  PossibleValues,
} from "../WidgetEditor/types";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from "@material-ui/core/Dialog";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import * as Immutable from "immutable";
import { Paper } from "@material-ui/core";

type QueryEditorFilterProps = {
  groupByAvailable: EventField[];
  filterItems: Immutable.List<QueryFilterKey>;
  possibleValues: PossibleValues;
  onAddFilterItem: (item: QueryFilterKey) => void;
  onRemoveFilterItem: (item: QueryFilterKey) => void;
};

function QueryEditorFilter(props: QueryEditorFilterProps) {
  const [open, setOpen] = React.useState(false);

  const keyOpHardcoded: { value: QueryFilterKeyOperation; label: string }[] = [
    { value: "eq", label: "==" },
    { value: "neq", label: "!=" },
  ];

  const valueOpHardcoded: {
    value: QueryFilterValueOperation;
    label: string;
  }[] = [
    { value: "contains", label: "contains" },
    { value: "notcontains", label: "notcontains" },
  ];

  const numberOpHardcoded: {
    value: QueryFilterNumberOperation;
    label: string;
  }[] = [
    { value: "gt", label: ">" },
    { value: "lt", label: "<" },
    { value: "gte", label: ">=" },
    { value: "lte", label: "<=" },
    { value: "eq", label: "==" },
    { value: "neq", label: "!=" },
  ];

  const [filterField, setFilterField] = React.useState("");
  const [filterOp, setFilterOp] = React.useState("");
  const [filterValue, setFilterValue] = React.useState("");

  const handleDelete = (item: QueryFilterKey) => () => {
    console.log("handleDelete " + JSON.stringify(item));
    props.onRemoveFilterItem(item);
  };

  const handleSelectField = (event: React.ChangeEvent<HTMLInputElement>) => {
    //setName(event.target.value);
    console.log("handleSelectField " + event.target.value);
    const item = props.groupByAvailable.find(
      (x) => x.name === event.target.value
    );

    console.log("handleSelectField item " + item?.name);
    const newValue =
      event.target.value && event.target.value !== "" ? event.target.value : "";
    //const newOp = item?.descriptor.type != "number" ? "count" : props.chart?.op;
    console.log("handleSelectField " + newValue);
    setFilterField(newValue);    

    
  };

  const handleSelectOp = (event: React.ChangeEvent<HTMLInputElement>) => {
    //setName(event.target.value);
    console.log("handleSelectOp " + event.target.value);
    const newOp =
      event.target.value && event.target.value !== "" ? event.target.value : "";
    console.log("newOp " + newOp);
    setFilterOp(newOp);
  };

  const handleSelectValue = (event: React.ChangeEvent<HTMLInputElement>) => {
    //setName(event.target.value);
    console.log("handleSelectValue " + event.target.value);
    const newValue =
      event.target.value && event.target.value !== "" ? event.target.value : "";
    console.log("newValue " + newValue);
    setFilterValue(newValue);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };
  
  const handleCancel = () => {
    setOpen(false);
  };

  const handleOk = () => {
    setOpen(false);

    const list = Immutable.List(props.groupByAvailable);
    const type: string = list
      .filter((x) => x.name === filterField)
      .map((x) => x.descriptor.type)
      .first() ?? "";

    const item: QueryFilterKey = {
      field: filterField,
      type: type,
      op: filterOp,
      value: filterValue,
    };

    props.onAddFilterItem(item);
    setFilterValue("");    
    setFilterOp("");
    setFilterField("");
  };

  const list = Immutable.List(props.groupByAvailable);
  const filterFieldType: string =
    list
      .filter((x) => x.name === filterField)
      .map((x) => x.descriptor.type)
      .first() ?? "";
  const possibleSet: string[] =
    filterFieldType === "set"
      ? props.possibleValues.set.has(filterField)
        ? props.possibleValues.set.get(filterField)!
        : []
      : [];

  const possibleDictionary: any[] =
    filterFieldType === "dictionary"
      ? props.possibleValues.dictionary.has(filterField)
        ? props.possibleValues.dictionary.get(filterField)!
        : []
      : [];

  // possibleDictionary.map((p, idx) => {
  //   //console.log("possibleDictionary [" + idx + "] = " + JSON.stringify(p));
  //   const key = p.get("key");
  //   const value = p.get("value");
  //   //console.log("possibleDictionary [" + key + "] = " + value);
  // })

  //console.log("possibleDictionary = " + JSON.stringify(possibleDictionary));
  const okEnabled = filterField !== "" && filterOp !== "" && filterValue !== "";

  const filterItemsList = Immutable.List(props.filterItems);
  console.log(filterItemsList);
  return (
    <Paper variant="elevation" elevation={0} title="Filter the result dataset">
      <Typography variant="h6">
        Filter
        <IconButton edge="end" aria-label="filter" onClick={handleClickOpen} title="Add filter">
          <FilterListOutlinedIcon />
        </IconButton>
      </Typography>
      {filterItemsList.map((item, index) => {
        const key = item.field + "." + item.op + "." + item.value;
        const itemTranslated: string =
          list
            .filter((x) => x.name === item.field)
            .map((x) => x.translation)
            .first() ?? item.field;
        const itemType: string =
          list
            .filter((x) => x.name === item.field)
            .map((x) => x.descriptor.type)
            .first() ?? "";
        const op =
          itemType === "number"
            ? Immutable.List(numberOpHardcoded)
                .filter((x) => x.value === item.op)
                .map((x) => x.label)
                .first() ?? item.op
            : itemType === "set" || itemType === "dictionary"
            ? Immutable.List(keyOpHardcoded)
                .filter((x) => x.value === item.op)
                .map((x) => x.label)
                .first() ?? item.op
            : itemType === "string"
            ? Immutable.List(valueOpHardcoded)
                .filter((x) => x.value === item.op)
                .map((x) => x.label)
                .first() ?? item.op
            : item.op;

        //TODO:    any,      ,         
        const values: any[] = props.possibleValues.dictionary.has(item.field)
          ? props.possibleValues.dictionary.get(item.field)!
          : [];

        //console.log("values = " + JSON.stringify(values));

        const value =
          itemType === "dictionary"
            ? Immutable.List(values)
                .filter((x) => x.key === item.value)
                .map((x) => x.value)
                .first() ?? item.value
            : item.value;

        const label = itemTranslated + " " + op + " " + value;

        return (
          <Chip
            key={key}
            variant="outlined"
            size="small"
            onDelete={handleDelete(item)}
            label={label}
          />
        );
      })}

      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        maxWidth="xs"
        fullWidth
        aria-labelledby="confirmation-dialog-title"
        open={open}
      >
        <DialogTitle id="confirmation-dialog-title">Filter by</DialogTitle>
        <DialogContent dividers>
          <Grid container spacing={2} direction="column" alignItems="stretch">
            <Grid item>
              <TextField
                id="field-textfield"
                label={
                  filterFieldType !== ""
                    ? "Field / " + filterFieldType
                    : "Field"
                }
                size="small"
                select
                fullWidth
                value={filterField}
                onChange={handleSelectField}
                variant="outlined"
              >
                {props.groupByAvailable.map((field, index) => {
                  if (field.descriptor.childs != null) {
                    return null;
                  } else {
                    return (
                      <MenuItem key={field.name} value={field.name}>
                        {field.translation}
                      </MenuItem>
                    );
                  }
                })}
              </TextField>
            </Grid>
            <Grid item>
              {filterFieldType === "number" ||
              filterFieldType === "string" ||
              filterFieldType === "set" ||
              filterFieldType === "dictionary" ? (
                <TextField
                  id="op-value-textfield"
                  label="op"
                  size="small"
                  select
                  fullWidth
                  value={filterOp}
                  onChange={handleSelectOp}
                  variant="outlined"
                >
                  {filterFieldType === "number"
                    ? numberOpHardcoded.map((op, index) => {
                        return (
                          <MenuItem key={op.value} value={op.value}>
                            {op.label}
                          </MenuItem>
                        );
                      })
                    : null}
                  {filterFieldType === "set" || filterFieldType === "dictionary"
                    ? keyOpHardcoded.map((op, index) => {
                        return (
                          <MenuItem key={op.value} value={op.value}>
                            {op.label}
                          </MenuItem>
                        );
                      })
                    : null}
                  {filterFieldType === "string"
                    ? valueOpHardcoded.map((op, index) => {
                        return (
                          <MenuItem key={op.value} value={op.value}>
                            {op.label}
                          </MenuItem>
                        );
                      })
                    : null}
                  {/* <MenuItem key="[no value]" value="">[No value]</MenuItem> */}
                </TextField>
              ) : null}
            </Grid>
            <Grid item>
              {filterFieldType === "number" || filterFieldType === "string" ? (
                <TextField
                  id="value-textfield"
                  label="Value"
                  size="small"
                  fullWidth
                  value={filterValue}
                  onChange={handleSelectValue}
                  variant="outlined"
                />
              ) : null}
              {filterFieldType === "set" ? (
                <TextField
                  id="value-textfield"
                  label="Value"
                  size="small"
                  fullWidth
                  select
                  value={filterValue}
                  onChange={handleSelectValue}
                  variant="outlined"
                >
                  <MenuItem key="" value="">
                    [No value]
                  </MenuItem>
                  {possibleSet.map((p) => {
                    return (
                      <MenuItem key={p} value={p}>
                        {p}
                      </MenuItem>
                    );
                  })}
                </TextField>
              ) : null}
              {filterFieldType === "dictionary" ? (
                <TextField
                  id="value-textfield"
                  label="Value"
                  size="small"
                  fullWidth
                  select
                  value={filterValue}
                  onChange={handleSelectValue}
                  variant="outlined"
                >
                  <MenuItem key="" value="">
                    [No value]
                  </MenuItem>
                  {possibleDictionary.map((p) => {
                    const key = p.key;
                    const value = p.value;
                    // const key = p.get("key");
                    // const value = p.get("value");
                    return (
                      <MenuItem key={key} value={key}>
                        {value}
                      </MenuItem>
                    );
                  })}
                </TextField>
              ) : null}
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCancel} color="primary">
            Cancel
          </Button>
          {okEnabled ? (
            <Button onClick={handleOk} color="primary">
              Ok
            </Button>
          ) : (
            <Button onClick={handleOk} disabled color="primary">
              Ok
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </Paper>
  );
}

export default QueryEditorFilter;
