import * as React from "react";
import Chip from "@material-ui/core/Chip";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import AddCircleOutlineOutlinedIcon from "@material-ui/icons/AddCircleOutlineOutlined";
import { EventField } from "../WidgetEditor/types";
import * as Immutable from "immutable";
import { Paper } from "@material-ui/core";

type QueryEditorSelectProps = {
  groupBy: Immutable.List<string>;
  groupByAvailable: EventField[];
  onAddField: (item: EventField) => void;
  onRemoveField: (item: EventField) => void;
};

function QueryEditorSelect(props: QueryEditorSelectProps) {
  //const [open, setOpen] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  // const open = Boolean(anchorEl);

  const handleDelete = (item: EventField) => () => {
    props.onRemoveField(item);
  };

  const handleOpenMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleSelect = (item: EventField) => () => {
    props.onAddField(item);
    setAnchorEl(null);
  };
  const handleCloseMenu = () => {
    setAnchorEl(null);
  };

  const created = props.groupBy;
  const available = props.groupByAvailable.filter(
    (x) =>
      !created.find((xx) => xx === x.name) && x.descriptor.type !== "complex"
  );

  const itemByName = (name: string) => {
    return props.groupByAvailable.find((x) => x.name === name)
  };

  return (
    <Paper variant="elevation" elevation={0} title="Select fields that will be used in the result dataset">
      <Typography variant="h6">
        Select
        <IconButton edge="end" aria-label="filter" onClick={handleOpenMenu} title="Add field" >
          <AddCircleOutlineOutlinedIcon />
        </IconButton>
      </Typography>

      {Immutable.List(props.groupBy).map((x) => itemByName(x)).filter((x) => !!x).map((x)=> x!).map((item) => (
        <Chip
          key={item.name}
          variant="outlined"
          size="small"
          onDelete={handleDelete(item)}
          label={item.translation}
        />
      ))}

      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleCloseMenu}
      >
        {available.map((item) => (
          <MenuItem key={item.name} onClick={handleSelect(item)}>
            {item.translation}
          </MenuItem>
        ))}
      </Menu>
    </Paper>
  );
}

export default QueryEditorSelect;
