import React from "react";
import Markdown from "markdown-to-jsx";
// import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
//import CardActions from '@material-ui/core/CardActions';
import CardContent from "@material-ui/core/CardContent";
//import Button from '@material-ui/core/Button';
// import Typography from "@material-ui/core/Typography";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import { CardActions, IconButton } from "@material-ui/core";

type Props = {
  isEditMode: boolean;
  id: string;
  title?: string;
  description?: string;

  onEditWidget?: ((id: string) => void) | undefined;
  onDeleteWidget?: ((id: string) => void) | undefined;
};

class WidgetMarkdown extends React.Component<Props> {
  handleEdit = () => {
    console.log("handleEdit " + this.props.id);
    if (this.props.onEditWidget) {
      this.props.onEditWidget(this.props.id);
    }
  };
  handleDelete = () => {
    console.log("handleDelete " + this.props.id);
    if (this.props.onDeleteWidget) {
      this.props.onDeleteWidget(this.props.id);
    }
  };

  render() {
    return (
      <Card variant="outlined" title={this.props.title}>
        <CardContent>
          {/* <Typography align="center" color="textSecondary" gutterBottom>
            {this.props.title}
          </Typography>           */}
          <Markdown>{this.props.description ?? ""}</Markdown>
        </CardContent>
        {this.props.isEditMode ? (
          <CardActions disableSpacing>
            <IconButton aria-label="edit" onClick={this.handleEdit}>
              <EditIcon />
            </IconButton>
            <IconButton aria-label="delete" onClick={this.handleDelete}>
              <DeleteIcon />
            </IconButton>
          </CardActions>
        ) : null}
      </Card>
    );
  }
}

export default WidgetMarkdown;
