//     minWidth: 650,
//   },
// });

const icon = (MatIcon: any) =>
  forwardRef<SVGSVGElement>((props, ref) => <MatIcon {...props} ref={ref} />);

const tableIcons = {
  Add: icon(AddBox),
  Check: icon(Check),
  Clear: icon(Clear),
  Delete: icon(DeleteOutline),
  DetailPanel: icon(ChevronRight),
  Edit: icon(Edit),
  Export: icon(SaveAlt),
  Filter: icon(FilterList),
  FirstPage: icon(FirstPage),
  LastPage: icon(LastPage),
  NextPage: icon(ChevronRight),
  PreviousPage: icon(ChevronLeft),
  ResetSearch: icon(Clear),
  Search: icon(Search),
  SortArrow: icon(ArrowDownward),
  ThirdStateCheck: icon(Remove),
  ViewColumn: icon(ViewColumn),
};

// const rows = sampleData.result; //.slice(0, 10);

type Props = {
  isEditMode: boolean;
  api: string;
  token: string;
  refreshTime?: Date;

  id: string;
  title?: string;
  description?: string;
  tableState: TableState;
  groupBy: Immutable.List<string>;
  groupByAvailable: EventField[];
  filterItems: Immutable.List<QueryFilterKey>;
  period: TimePeriod;
  possibleValues: PossibleValues;

  onEditWidget?: ((id: string) => void) | undefined;
  onDeleteWidget?: ((id: string) => void) | undefined;
};

type State = {
  rows?: any;
  err?: string;
  openFullscreen: boolean;
  selected?: string;
};

class WidgetAggregationTable extends React.Component<Props, State> {
  state: State = {
    rows: undefined,
    err: undefined,
    openFullscreen: false,
    selected: undefined,
  };

  handleFullscreenOpen = () => {
    this.setState((prevState) => {
      return {
        ...prevState,
        openFullscreen: true,
      };
    });
  };

  handleFullscreenClose = () => {
    this.setState((prevState) => {
      return {
        ...prevState,
        openFullscreen: false,
      };
    });
  };

  handleEdit = () => {
    console.log("handleEdit " + this.props.id);
    if (this.props.onEditWidget) {
      this.props.onEditWidget(this.props.id);
    }
  };
  handleDelete = () => {
    console.log("handleDelete " + this.props.id);
    if (this.props.onDeleteWidget) {
      this.props.onDeleteWidget(this.props.id);
    }
  };

  loadData = () => {
    console.log("WidgetAggregationTable loadData");
    this.setState({ rows: undefined, err: undefined });
    //Simple POST request with a JSON body using fetch
    const url1 = this.props.api + "/api/v2/ad-query/aggregation/table";

    const itemByName = (name: string) => {
      return this.props.groupByAvailable.find((x) => x.name === name);
    };

    const groupByItems = Immutable.List(this.props.groupBy)
      .map((x) => itemByName(x))
      .filter((x) => !!x)
      .map((x) => x!);

    const aggregateByField =
      this.props.tableState.field ??
      Immutable.List(this.props.groupBy).first() ??
      "";
    const aggregateByFieldType: string =
      groupByItems
        .filter((x) => x.name === aggregateByField)
        .map((x) => x.descriptor.type)
        .first() ?? "";

    const groupBy = this.props.tableState.isAggregationFieldInGroupBy
      ? this.props.groupBy.toArray()
      : this.props.groupBy.filter((x) => x !== aggregateByField).toArray();

    const valueOpHardcoded: EventFieldOp[] = [
      "count",
      "avg",
      "sum",
      "min",
      "max",
    ];
    const availableOp =
      aggregateByFieldType !== "number" ? ["count"] : valueOpHardcoded;
    const currentOpAvailable = availableOp.includes(
      this.props.tableState.op ?? ""
    );
    const aggregateByFieldOp: EventFieldOp = currentOpAvailable
      ? this.props.tableState.op ?? "count"
      : "count";

    const d = {
      filter: {
        keys: this.props.filterItems.filter(
          (x) => x.type === "set" || x.type === "dictionary"
        ),
        numbers: this.props.filterItems
          .filter((x) => x.type === "number")
          .map((x) => {
            return {
              field: x.field,
              op: x.op,
              value: Number.parseFloat(x.value),
            };
          }),
        lists: [],
        period: this.props.period,
      },
      groupBy: groupBy,
      fields: [
        {
          field: aggregateByField,
          op: aggregateByFieldOp,
        },
      ],
    };
    const requestOptions: RequestInit = {
      method: "POST",
      //mode: "no-cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.props.token,
      },
      body: JSON.stringify(d), //JSON.stringify({ title: 'React POST Request Example' })
    };
    fetch(url1, requestOptions)
      //.then(r => r.json().then(json => ({ok: r.ok, status: r.status, json})))
      //.then( r => r.ok ? r.json: Promise.reject(r))
      .then((response) => response.json())
      .then((data) => {
        //this.props.onChangeEventFields(data);
        if (data.result == null) {
          throw Error("no rows in result" + JSON.stringify(data));
        } else {
          const r = data.result.map((item: any) => {
            return item;
          });
          this.setState({ rows: r });
        }
        console.log("WidgetAggregationTable data=" + JSON.stringify(data));
      })
      .catch((e) => {
        this.setState({ err: e.message });
      });
  };
  componentDidMount() {
    console.log(
      "WidgetAggregationTable componentDidMount " + this.props.tableState.field
    );
    this.loadData();
  }

  componentDidUpdate(prevProps: Props) {
    console.log(
      "WidgetAggregationTable componentDidUpdate " + this.props.tableState.field
    );
    if (JSON.stringify(this.props) !== JSON.stringify(prevProps)) {
      // Check if it's a new user, you can also use some unique property, like the ID  (this.props.user.id !== prevProps.user.id)
      this.loadData();
    }
  }

  render() {
    const itemByName = (name: string) => {
      return this.props.groupByAvailable.find((x) => x.name === name);
    };

    const groupByItems = Immutable.List(this.props.groupBy)
      .map((x) => itemByName(x))
      .filter((x) => !!x)
      .map((x) => x!);

    const aggregateByField =
      this.props.tableState.field ??
      Immutable.List(this.props.groupBy).first() ??
      "";
    const aggregateByFieldType: string =
      groupByItems
        .filter((x) => x.name === aggregateByField)
        .map((x) => x.descriptor.type)
        .first() ?? "";
    const aggregateByFieldTranslation: string =
      groupByItems
        .filter((x) => x.name === aggregateByField)
        .map((x) => x.translation)
        .first() ?? aggregateByField;

    const valueOpHardcoded: EventFieldOp[] = [
      "count",
      "avg",
      "sum",
      "min",
      "max",
    ];
    const availableOp =
      aggregateByFieldType !== "number" ? ["count"] : valueOpHardcoded;
    const currentOpAvailable = availableOp.includes(
      this.props.tableState.op ?? ""
    );
    const aggregateByFieldOp: EventFieldOp = currentOpAvailable
      ? this.props.tableState.op ?? "count"
      : "count";

    const columns = groupByItems
      .filter((x) =>
        this.props.tableState.isAggregationFieldInGroupBy
          ? true
          : x.name !== aggregateByField
      )
      .map((item) => {
        var itemValues: PossibleValueDictionary[] = [];
        if (item.descriptor.type === "dictionary") {
          itemValues =
            this.props.possibleValues.dictionary.get(item.name) || [];
          itemValues = itemValues.map((xx) => {
            return {
              key: xx.key,
              value: xx.value && xx.value !== "" ? xx.value : xx.key,
            };
          });
        }
        const itemLookup = itemValues.reduce((coll: any, item: any) => {
          const currentX = item.key;
          const valueForX = item.value;
          coll.set(currentX, valueForX);
          return coll;
        }, new Map());
        const itemLookupObject = Object.fromEntries(itemLookup.entries());
        console.log(itemLookupObject);

        return {
          field: item.name,
          title: item.translation,
          lookup:
            item.descriptor.type === "dictionary"
              ? itemLookupObject
              : undefined,
        };
      });

    const aggColumn: any = {
      field: aggregateByFieldOp + "_" + aggregateByField,
      title: aggregateByFieldOp + "(" + aggregateByFieldTranslation + ")",
    };
    const allColumns = columns.toArray().concat(aggColumn);

    if (this.state.err) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>Error: {this.state.err}</Typography>
          </CardContent>
        </Card>
      );
    }

    const theTable = (
      <MaterialTable
        icons={tableIcons}
        columns={allColumns}
        // localization={{
        //   toolbar: { exportName: "Export" },
        // }}
        onColumnDragged={(sourceIndex: number, destinationIndex: number) => {
          console.log(
            "Dropped column from " +
              sourceIndex +
              " to position " +
              destinationIndex
          );
        }}
        options={{
          showTitle: false,
          padding: "dense",
          exportAllData: true,
          exportButton: true,
          exportFileName:
            this.props.title + "." + moment().format("YYYYMMDDTHHmmss"),
          // exportCsv: (columns, data) => {
          //   alert(
          //     "You should develop a code to export " + data.length + " rows"
          //   );
          // },
          rowStyle: (rowData) => ({
            backgroundColor:
              this.state.selected === rowData.tableData.id ? "#EEE" : "#FFF",
          }),
        }}
        onRowClick={(evt, selectedRow: any) =>
          this.setState({ selected: selectedRow?.tableData.id })
        }
        title={this.props.title}
        isLoading={!this.state.rows && !this.state.err}
        data={this.state.rows}
        components={{
          Container: (props) => <Paper {...props} elevation={0} />,
        }}
      />
    );

    return (
      <div id={this.props.id}>
        <Card variant="outlined" title={this.props.title}>
          <CardHeader
            action={
              <IconButton
                aria-label="fullscreen"
                onClick={this.handleFullscreenOpen}
              >
                <FullscreenIcon />
              </IconButton>
              // <IconButton aria-label="settings">
              //   <MoreVertIcon />
              // </IconButton>
            }
            title={this.props.title}
            subheader={this.props.description}
          />
          <CardContent>{theTable}</CardContent>
          {this.props.isEditMode ? (
            <CardActions disableSpacing>
              <IconButton aria-label="edit" onClick={this.handleEdit}>
                <EditIcon />
              </IconButton>
              <IconButton aria-label="delete" onClick={this.handleDelete}>
                <DeleteIcon />
              </IconButton>
            </CardActions>
          ) : null}
        </Card>
        <Dialog
          fullScreen
          open={this.state.openFullscreen}
          onClose={this.handleFullscreenClose}
        >
          <DialogTitle id="editor-dialog-title">
            <Toolbar variant="dense">
              <IconButton
                edge="start"
                color="inherit"
                onClick={this.handleFullscreenClose}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
              <Typography variant="h6">{this.props.title}</Typography>
              {/* <Button color="inherit" onClick={this.handleDownloadPdf}>
                PDF
              </Button>
              <Button color="inherit" onClick={this.handleDownloadSvg}>
                SVG
              </Button>
              <Button color="inherit" onClick={this.handleDownloadPng}>
                PNG
              </Button> */}
            </Toolbar>
          </DialogTitle>
          <DialogContent>
            <Typography variant="h6">{this.props.description}</Typography>
            {theTable}
          </DialogContent>
        </Dialog>
      </div>
    );

    // return (
    //   <Card variant="outlined" title={this.props.title}>
    //     <CardContent>
    //       <MaterialTable
    //         icons={tableIcons}
    //         columns={allColumns}
    //         data={this.state.rows}
    //         title={
    //           <Container>
    //             <Typography variant="h5" component="h5">
    //               {this.props.title}
    //             </Typography>
    //             <Typography color="textSecondary">
    //               {this.props.description}
    //             </Typography>
    //           </Container>
    //         }
    //       />
    //     </CardContent>
    //   </Card>
    // );
  }
}

export default WidgetAggregationTable;
