import * as React from "react";
// import Breadcrumbs from "@material-ui/core/Breadcrumbs";
// import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Paper from "@material-ui/core/Paper";
// import Container from "@material-ui/core/Paper";
import { Grid } from "@material-ui/core";

type WidgetEditorTitleProps = {
  title?: string;
  description?: string;
  onChangeTitle: (title: string) => void;
  onChangeDescription: (description: string) => void;
};

function WidgetEditorTitle(props: WidgetEditorTitleProps) {

  const handleTitleValue = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue =
      event.target.value && event.target.value !== "" ? event.target.value : "";
    console.log("newTitleValue " + newValue);
    props.onChangeTitle(newValue);
  };

  const handleDescriptionValue = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue =
      event.target.value && event.target.value !== "" ? event.target.value : "";
    console.log("newDescriptionValue " + newValue);
    props.onChangeDescription(newValue);
  };

  return (
    <Paper variant="elevation" elevation={0}>
      <Grid container spacing={2} direction="column" alignItems="stretch">
        <Grid item spacing={2}>
          <div>
            <TextField
              id="title-text"
              size="small"
              label="Title"              
              value={props.title}
              onChange={handleTitleValue}
              fullWidth
              InputLabelProps={{ shrink: true }}
              variant="outlined"
            />
          </div>
        </Grid>
        <Grid item spacing={2}>
          <TextField
            id="description-text"
            size="small"
            label="Description"
            value={props.description}
            onChange={handleDescriptionValue}
            fullWidth
            multiline
            variant="outlined"
          />
        </Grid>
      </Grid>
    </Paper>
  );
}

export default WidgetEditorTitle;
