import * as React from "react";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Grid from "@material-ui/core/Grid";
import * as Immutable from "immutable";
import {
  EventField,
  ChartState,
  EventFieldOp,
  ChartType,
  PivotTableState,
  PossibleValues,
  PossibleValueDictionary,
  QueryFilterNumberOperation,
} from "../WidgetEditor/types";
import { FormControlLabel, Switch, Checkbox } from "@material-ui/core";
import RemoveIcon from '@material-ui/icons/Remove';

type Props = {
  pivot?: PivotTableState;
  groupBy: Immutable.List<string>;
  groupByAvailable: EventField[];
  possibleValues: PossibleValues;
  onPivotTableChange: (pivotTable: PivotTableState) => void;
};

function VisualizeTypePivotTable(props: Props) {
  const valueOpHardcoded: EventFieldOp[] = [
    "count",
    "avg",
    "sum",
    "min",
    "max",
  ];

  const itemByName = (name: string) => {
    return props.groupByAvailable.find((x) => x.name === name);
  };

  const handleSelectRows = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectRows " + event.target.value);
    const item = props.groupBy
      .map((x) => itemByName(x))
      .filter((x) => !!x)
      .map((x) => x!)
      .find((x) => x.name === event.target.value);
    console.log("handleSelectRows item " + item?.name);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newPivot = { ...props.pivot, rows: newValue };
    props.onPivotTableChange(newPivot);
  };
  const handleSelectColumns = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectColumns " + event.target.value);
    const item = props.groupBy
      .map((x) => itemByName(x))
      .filter((x) => !!x)
      .map((x) => x!)
      .find((x) => x.name === event.target.value);
    console.log("handleSelectColumns item " + item?.name);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newPivot = { ...props.pivot, columns: newValue };
    props.onPivotTableChange(newPivot);
  };

  const handleSelectValues = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectValues " + event.target.value);
    const item = props.groupBy
      .map((x) => itemByName(x))
      .filter((x) => !!x)
      .map((x) => x!)
      .find((x) => x.name === event.target.value);
    console.log("handleSelectValues item " + item?.name);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newOp =
      item?.descriptor.type !== "number" ? "count" : props.pivot?.op;
    const newPivot = { ...props.pivot, values: newValue, op: newOp };
    props.onPivotTableChange(newPivot);
  };

  const handleSelectValuesOp = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectValuesOp " + event.target.value);
    const newPivot = { ...props.pivot, op: event.target.value as EventFieldOp };
    props.onPivotTableChange(newPivot);
  };
  const handleFixed1 = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleFixed1 " + event.target.value);
    const x: boolean = event.target.checked;
    const newPivot = { ...props.pivot, fixed1: x };
    props.onPivotTableChange(newPivot);
  };
  const handleDeltaColumn = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleFixed1 " + event.target.value);
    const x: boolean = event.target.checked;
    const newPivot = { ...props.pivot, delta: x };
    props.onPivotTableChange(newPivot);
  };
  const handleDeltaColumnTitle = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleDeltaColumnTitle " + event.target.value);
    const newPivot = { ...props.pivot, deltaTitle: event.target.value };
    props.onPivotTableChange(newPivot);
  };
  const handleDeltaColumnOnly = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleFixed1 " + event.target.value);
    const x: boolean = event.target.checked;
    const newPivot = { ...props.pivot, deltaOnly: x };
    props.onPivotTableChange(newPivot);
  };
  
  const handleSelectDeltaA = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectDeltaA " + event.target.value);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newPivot = { ...props.pivot, deltaA: newValue };
    props.onPivotTableChange(newPivot);
  };
  const handleSelectDeltaB = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectDeltaB " + event.target.value);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newPivot = { ...props.pivot, deltaB: newValue };
    props.onPivotTableChange(newPivot);
  };
  const handleWithReferenceValue = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleWithReferenceValue " + event.target.value);
    const x: boolean = event.target.checked;
    const newPivot = {
      ...props.pivot,
      referenceValue: x ? 0 : undefined,
      referenceOp: x ? props.pivot?.referenceOp ?? "eq" : undefined,
    };
    props.onPivotTableChange(newPivot);
  };
  const handleReferenceColumn = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleReferenceColumn " + event.target.value);
    const newPivot = {
      ...props.pivot,
      referenceColumn: event.target.value,
    };
    props.onPivotTableChange(newPivot);
  };
  const handleReferenceValue = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleReferenceValue " + event.target.value);
    const newPivot = {
      ...props.pivot,
      referenceValue: Number(event.target.value),
    };
    props.onPivotTableChange(newPivot);
  };
  const handleReferenceOp = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleReferenceOp " + event.target.value);
    const newPivot: PivotTableState = {
      ...props.pivot,
      // field: this.props.counter.field,
      // fieldGroupBy: this.props.counter.fieldGroupBy,
      referenceOp: event.target.value as QueryFilterNumberOperation,
    };
    props.onPivotTableChange(newPivot);
  };

  const availableFieldsRows = props.groupBy
    .map((x) => itemByName(x))
    .filter((x) => !!x)
    .map((x) => x!)
    .filter(
      (x) =>
        !(x.name === props.pivot?.values || x.name === props.pivot?.columns)
    );
  const availableFieldsColumns = props.groupBy
    .map((x) => itemByName(x))
    .filter((x) => !!x)
    .map((x) => x!)
    .filter(
      (x) => !(x.name === props.pivot?.rows || x.name === props.pivot?.values)
    );
  const availableFieldsValues = props.groupBy
    .map((x) => itemByName(x))
    .filter((x) => !!x)
    .map((x) => x!)
    .filter(
      (x) => !(x.name === props.pivot?.rows || x.name === props.pivot?.columns)
    );

  const groupByItems = Immutable.List(props.groupBy)
    .map((x) => itemByName(x))
    .filter((x) => !!x)
    .map((x) => x!);
  const rowsType: string =
    groupByItems
      .filter((x) => x.name === props.pivot?.rows)
      .map((x) => x.descriptor.type)
      .first() ?? "";
  const columnsType: string =
    groupByItems
      .filter((x) => x.name === props.pivot?.columns)
      .map((x) => x.descriptor.type)
      .first() ?? "";
  const valuesType: string =
    groupByItems
      .filter((x) => x.name === props.pivot?.values)
      .map((x) => x.descriptor.type)
      .first() ?? "";
  

  const availableOp = valuesType !== "number" ? ["count"] : valueOpHardcoded;

  //for delta column we need all possible column names (from possibleValues)  
  const columnsFieldName: string = props.pivot?.columns ?? "";
  const columnsFieldType: string = columnsType;
  var columnsValues: PossibleValueDictionary[] = [];
  if (columnsFieldType === "set") {
    columnsValues =
      props.possibleValues.set.get(columnsFieldName)?.map((xx) => {
        return {
          key: xx,
          value: xx,
        };
      }) || [];
  } else if (columnsFieldType === "dictionary") {
    columnsValues =
      props.possibleValues.dictionary.get(columnsFieldName) || [];
    columnsValues = columnsValues.map((xx) => {
      return {
        key: xx.key,
        value: xx.value && xx.value !== "" ? xx.value : xx.key,
      };
    });
  }

  console.log("columnsValues = " + JSON.stringify(columnsValues));
  const availableDeltaA = columnsValues; //.filter((c) => c.key !== props.pivot?.deltaB);
  const availableDeltaB = columnsValues; //.filter((c) => c.key !== props.pivot?.deltaA);
  const anyColumn = {
    key: "*",
    value: "*",
  };
  const availableReference = [anyColumn].concat(columnsValues);

  const numberOpHardcoded: {
    value: QueryFilterNumberOperation;
    label: string;
  }[] = [
    { value: "gt", label: ">" },
    { value: "lt", label: "<" },
    { value: "gte", label: ">=" },
    { value: "lte", label: "<=" },
    { value: "eq", label: "==" },
    { value: "neq", label: "!=" },
  ];

  const withReferenceValue: boolean = props.pivot?.referenceValue !== undefined;

  return (
    <div title="VisualizeTypePivotTable">
      <Typography variant="h6">Pivot table</Typography>
      <Typography variant="subtitle2">&nbsp;</Typography>
      <Grid container spacing={2} direction="column" alignItems="stretch">
        <Grid item>
          <TextField
            id="rows-outlined-text"
            label={rowsType !== "" ? "Rows Field / " + rowsType : "Rows Field"}
            size="small"
            select
            fullWidth
            value={props.pivot?.rows ?? ""}
            onChange={handleSelectRows}
            variant="outlined"
          >
            <MenuItem key="[no value]" value="">
              [ No value ]
            </MenuItem>
            {availableFieldsRows.map((item) => (
              <MenuItem key={item.name} value={item.name}>
                {item.translation}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item>
          <TextField
            id="columns-outlined-text"
            label={
              columnsType !== ""
                ? "Columns Field / " + columnsType
                : "Columns Field"
            }
            size="small"
            select
            fullWidth
            value={props.pivot?.columns ?? ""}
            onChange={handleSelectColumns}
            variant="outlined"
          >
            <MenuItem key="[no value]" value="">
              [ No value ]
            </MenuItem>
            {availableFieldsColumns.map((item) => (
              <MenuItem key={item.name} value={item.name}>
                {item.translation}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item>
          <TextField
            id="values-outlined-text"
            label={valuesType !== "" ? "Values Field / " + valuesType : "Values Field"}
            size="small"
            select
            fullWidth
            value={props.pivot?.values ?? ""}
            onChange={handleSelectValues}
            variant="outlined"
          >
            <MenuItem key="[no value]" value="">
              [ No value ]
            </MenuItem>
            {availableFieldsValues.map((item) => (
              <MenuItem key={item.name} value={item.name}>
                {item.translation}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item>
          <TextField
            id="values-op-outlined-text"
            label="Value op"
            size="small"
            select
            fullWidth
            value={props.pivot?.op}
            onChange={handleSelectValuesOp}
            variant="outlined"
          >
            {availableOp.map((option) => (
              <MenuItem key={option} value={option}>
                {option}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item>
          <FormControlLabel
            control={
              <Switch
                checked={props.pivot?.fixed1 ?? false}
                onChange={handleFixed1}
                size="small"
              />
            }
            label="Fixed 1 column"
          />
        </Grid>
        <Grid item>
          <Grid container spacing={2} direction="row" alignItems="center" justify="flex-start">
            <Grid item xs={3} sm={3}>
              <FormControlLabel
                control={
                  <Switch
                    checked={props.pivot?.delta ?? false}
                    onChange={handleDeltaColumn}
                    size="small"
                  />
                }
                label="Delta column"
              />
            </Grid>
            {(props.pivot?.delta ?? false) ? (
            <Grid item>
              <TextField
                id="delta-title-outlined-text"
                label="Title"
                size="small"
                fullWidth
                defaultValue=""
                value={props.pivot?.deltaTitle}
                onChange={handleDeltaColumnTitle}
                variant="outlined" />
            </Grid>
            ) : null }
            {(props.pivot?.delta ?? false) ? (
            <Grid item>
              {/* <FormControlLabel
                control={
                  <Switch
                    checked={props.pivot?.deltaOnly ?? false}
                    onChange={handleDeltaColumnOnly}
                    size="small"
                  />
                }
                label="Hide others"
              /> */}
              <FormControlLabel
                control={
                  <Checkbox
                    checked={props.pivot?.deltaOnly ?? false}
                    onChange={handleDeltaColumnOnly}
                    name="deltaOnly"
                  />
                }
                label="Hide others"
              />
            </Grid>
            ) : null }
          </Grid>
        </Grid>
        {(props.pivot?.delta ?? false) ? (
        <Grid item>
            <Grid container spacing={2} direction="row" alignItems="center" justify="flex-start">
              <Grid item xs={3} sm={3}>
                <TextField
                  id="values-op-outlined-text"
                  label="A"
                  size="small"
                  select
                  fullWidth
                  value={props.pivot?.deltaA}
                  onChange={handleSelectDeltaA}
                  variant="outlined"
                >
                  {availableDeltaA.map((option) => (
                    <MenuItem key={option.key} value={option.value}>
                      {option.value}
                    </MenuItem>
                  ))}
                </TextField>
              </Grid>
              <Grid item justify="center" alignItems="center">
                <RemoveIcon/>
              </Grid>
              <Grid item xs={3} sm={3}>
                <TextField
                  id="values-op-outlined-text"
                  label="B"
                  size="small"
                  select
                  fullWidth
                  value={props.pivot?.deltaB}
                  onChange={handleSelectDeltaB}
                  variant="outlined"
                >
                  {availableDeltaB.map((option) => (
                    <MenuItem key={option.key} value={option.value}>
                      {option.value}
                    </MenuItem>
                  ))}
                </TextField>
              </Grid>
            </Grid>
        </Grid>
        ) : null }
        
        <Grid item>
            <FormControlLabel
              control={
                <Switch
                  checked={withReferenceValue}
                  onChange={handleWithReferenceValue}
                  size="small"
                />
              }
              label="Highlight row if"
            />
          </Grid>
          {withReferenceValue ? (
            <Grid item>              
              <Grid container spacing={2} direction="row" alignItems="stretch">
                <Grid item xs={3} sm={3}>
                  <TextField
                    id="reference-outlined-column"
                    label="Column"
                    size="small"
                    select
                    fullWidth
                    value={props.pivot?.referenceColumn ?? "*"}
                    onChange={handleReferenceColumn}                    
                    variant="outlined"
                  >
                    {availableReference.map((option) => (
                      <MenuItem key={option.key} value={option.value}>
                        {option.value}
                      </MenuItem>
                    ))}
                  </TextField>
                </Grid>
                <Grid item xs={3} sm={3}>
                  <TextField
                    id="op-value-textfield"
                    label="compare operation"
                    size="small"
                    select
                    fullWidth
                    value={props.pivot?.referenceOp}
                    onChange={handleReferenceOp}
                    variant="outlined"
                  >
                    {numberOpHardcoded.map((op, index) => {
                      return (
                        <MenuItem key={op.value} value={op.value}>
                          {op.label}
                        </MenuItem>
                      );
                    })}
                  </TextField>
                </Grid>
                <Grid item xs={3} sm={3}>
                  <TextField
                    id="reference-outlined-number"
                    label="Reference"
                    size="small"
                    fullWidth
                    value={props.pivot?.referenceValue}
                    onChange={handleReferenceValue}
                    type="number"
                    InputLabelProps={{
                      shrink: true,
                    }}
                    variant="outlined"
                  />
                </Grid>
              </Grid>
            </Grid>
          ) : null}
      </Grid>
    </div>
  );
}

export default VisualizeTypePivotTable;
