type Props = {
  isEditMode: boolean;
  api: string;
  token: string;
  refreshTime?: Date;

  id: string;
  title?: string;
  description?: string;
  counterState: CounterState;
  filterItems: Immutable.List<QueryFilterKey>;
  period: TimePeriod;
  onEditWidget?: ((id: string) => void) | undefined;
  onDeleteWidget?: ((id: string) => void) | undefined;
};

type State = {
  value?: string;
  err?: string;
  openFullscreen: boolean;
};

class WidgetCounter extends React.Component<Props, State> {
  state: State = {
    value: undefined,
    err: undefined,
    openFullscreen: false,
  };

  handleFullscreenOpen = () => {
    this.setState((prevState) => {
      return {
        ...prevState,
        openFullscreen: true,
      };
    });
  };

  handleFullscreenClose = () => {
    this.setState((prevState) => {
      return {
        ...prevState,
        openFullscreen: false,
      };
    });
  };

  handleEdit = () => {
    console.log("handleEdit " + this.props.id);
    if (this.props.onEditWidget) {
      this.props.onEditWidget(this.props.id);
    }
  };
  handleDelete = () => {
    console.log("handleDelete " + this.props.id);
    if (this.props.onDeleteWidget) {
      this.props.onDeleteWidget(this.props.id);
    }
  };

  handleRefresh = () => {
    console.log("handleRefresh");
    this.loadCounter();
  };

  loadCounter = () => {
    console.log("WidgetCounter loadCounter " + this.props.counterState.field);
    this.setState({ value: undefined, err: undefined });
    //Simple POST request with a JSON body using fetch
    const url1 = this.props.api + "/api/v2/ad-query/aggregation/table";

    const d = {
      filter: {
        keys: this.props.filterItems.filter(
          (x) => x.type === "set" || x.type === "dictionary"
        ),
        numbers: this.props.filterItems
          .filter((x) => x.type === "number")
          .map((x) => {
            return {
              field: x.field,
              op: x.op,
              value: Number.parseFloat(x.value),
            };
          }),
        lists: [],
        period: this.props.period,
      },
      groupBy: [
        this.props.counterState.fieldGroupBy ?? this.props.counterState.field,
      ],
      fields: [
        {
          field: this.props.counterState.field,
          op: this.props.counterState.op,
        },
      ],
    };
    const requestOptions: RequestInit = {
      method: "POST",
      //mode: "no-cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.props.token,
      },
      body: JSON.stringify(d), //JSON.stringify({ title: 'React POST Request Example' })
    };
    fetch(url1, requestOptions)
      //.then(r => r.json().then(json => ({ok: r.ok, status: r.status, json})))
      //.then( r => r.ok ? r.json: Promise.reject(r))
      .then((response) => response.json())
      .then((data) => {
        //this.props.onChangeEventFields(data);
        if (data.result.length !== 1) {
          throw Error(
            "rows in result == " +
              data.result.length +
              " " +
              JSON.stringify(data.result)
          );
        } else {
          let item = data.result[0];
          let value =
            item[
              this.props.counterState.op + "_" + this.props.counterState.field
            ];
          this.setState({ value: value });
          console.log({ id: this.props.id, value: value });
        }
        console.log("WidgetCounter data=" + JSON.stringify(data));
      })
      .catch((e) => {
        this.setState({ err: e.message });
      });
  };
  componentDidMount() {
    console.log(
      "WidgetCounter componentDidMount " + this.props.counterState.field
    );
    this.loadCounter();
  }

  componentDidUpdate(prevProps: Props) {
    console.log(
      "WidgetCounter componentDidUpdate " + this.props.counterState.field
    );
    if (JSON.stringify(this.props) !== JSON.stringify(prevProps)) {
      // Check if it's a new user, you can also use some unique property, like the ID  (this.props.user.id !== prevProps.user.id)
      this.loadCounter();
    }
  }

  render() {
    //const { classes } = this.props;
    //const classes = useStyles();
    //const bull = <span className={classes.bullet}></span>;

    if (this.state.err) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>Error: {this.state.err}</Typography>
          </CardContent>
        </Card>
      );
    }

    const withReferenceValue: boolean =
      this.props.counterState.referenceValue !== undefined;

    var isAlert = false;
    if (withReferenceValue && this.props.counterState.referenceOp) {
      const referenceValue: number = this.props.counterState.referenceValue!;
      switch (this.props.counterState.referenceOp!) {
        case "eq":
          isAlert = Number(this.state.value) === referenceValue;
          break;
        case "neq":
          isAlert = Number(this.state.value) !== referenceValue;
          break;
        case "gt":
          isAlert = Number(this.state.value) > referenceValue;
          break;
        case "lt":
          isAlert = Number(this.state.value) < referenceValue;
          break;
        case "gte":
          isAlert = Number(this.state.value) >= referenceValue;
          break;
        case "lte":
          isAlert = Number(this.state.value) <= referenceValue;
          break;
        default:
          break;
      }
    }
    return (
      <div id={this.props.id}>
        <Card variant="outlined">
          <CardHeader
            action={
              <IconButton
                aria-label="fullscreen"
                onClick={this.handleFullscreenOpen}
              >
                <FullscreenIcon />
              </IconButton>
            }
            color={isAlert ? "error" : "textSecondary"}
            title={this.props.title}
          />
          <CardContent>
            {/* <Typography
              align="center"
              color={isAlert ? "error" : "textSecondary"}
              gutterBottom
            >
              {this.props.title}
            </Typography> */}
            <Typography
              align="center"
              variant="h4"
              component="h4"
              color={isAlert ? "error" : "inherit"}
            >
              {this.state.value ?? "..."}
            </Typography>
            <Typography
              align="center"
              color={isAlert ? "error" : "textSecondary"}
            >
              {this.props.description}
            </Typography>
          </CardContent>
          {this.props.isEditMode ? (
            <CardActions disableSpacing>
              <IconButton aria-label="edit" onClick={this.handleEdit}>
                <EditIcon />
              </IconButton>
              <IconButton aria-label="delete" onClick={this.handleDelete}>
                <DeleteIcon />
              </IconButton>
            </CardActions>
          ) : null}
        </Card>
        <Dialog
          fullScreen
          open={this.state.openFullscreen}
          onClose={this.handleFullscreenClose}
        >
          <DialogTitle id="editor-dialog-title">
            <Toolbar variant="dense">
              <IconButton
                edge="start"
                color="inherit"
                onClick={this.handleFullscreenClose}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
              <Typography variant="h6">{this.props.title}</Typography>
              <IconButton
                aria-label="refresh"
                aria-haspopup="true"
                edge="end"
                color="inherit"
                onClick={this.handleRefresh}
              >
                <RefreshIcon />
              </IconButton>
            </Toolbar>
          </DialogTitle>
          <DialogContent>
            <div
              style={{
                height: "90vh",
                margin: 0,
                padding: 0,
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <div>
                <Typography
                  align="center"
                  variant="h1"
                  component="h1"
                  color={isAlert ? "error" : "inherit"}
                >
                  {this.state.value ?? "..."}
                </Typography>
                <Typography
                  align="center"
                  color={isAlert ? "error" : "textSecondary"}
                >
                  {this.props.description}
                </Typography>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>
    );
  }
}

export default WidgetCounter;
