import * as React from "react";
// import Breadcrumbs from "@material-ui/core/Breadcrumbs";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import { Grid, MenuItem, FormControlLabel, Switch } from "@material-ui/core";
import {
  CounterState,
  EventField,
  EventFieldOp,
  QueryFilterNumberOperation,
} from "../WidgetEditor/types";
import * as Immutable from "immutable";

type VisualizeTypeCounterProps = {
  counter?: CounterState;
  groupBy: Immutable.List<string>;
  groupByAvailable: EventField[];
  onCounterChange: (counter: CounterState) => void;
};

const valueOpHardcoded: EventFieldOp[] = ["count", "avg", "sum", "min", "max"];

class VisualizeTypeCounter extends React.Component<VisualizeTypeCounterProps> {
  handleSelectField = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectField " + event.target.value);
    const newCounter: CounterState = {
      ...this.props.counter,
      field: event.target.value,
      // fieldGroupBy: this.props.counter.fieldGroupBy,
      op: this.props.counter?.op ?? "count",
    };
    this.props.onCounterChange(newCounter);
  };

  handleSelectFieldGroupBy = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectFieldGroupBy " + event.target.value);
    const newCounter: CounterState = {
      ...this.props.counter,
      // field: this.props.counter.field,
      fieldGroupBy: event.target.value,
      op: this.props.counter?.op ?? "count",
    };
    this.props.onCounterChange(newCounter);
  };

  handleSelectOp = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectYOp " + event.target.value);
    const newCounter: CounterState = {
      ...this.props.counter,
      // field: this.props.counter.field,
      // fieldGroupBy: this.props.counter.fieldGroupBy,
      op: event.target.value as EventFieldOp,
    };
    this.props.onCounterChange(newCounter);
  };

  handleWithReferenceValue = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleWithReferenceValue " + event.target.value);
    const x: boolean = event.target.checked;
    const newCounter = {
      ...this.props.counter,
      referenceValue: x ? 0 : undefined,
      referenceOp: x ? this.props.counter?.referenceOp ?? "eq" : undefined,
    };
    this.props.onCounterChange(newCounter);
  };
  handleReferenceValue = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleReferenceValue " + event.target.value);
    const newCounter = {
      ...this.props.counter,
      referenceValue: Number(event.target.value),
    };
    this.props.onCounterChange(newCounter);
  };
  handleReferenceOp = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectYOp " + event.target.value);
    const newCounter: CounterState = {
      ...this.props.counter,
      // field: this.props.counter.field,
      // fieldGroupBy: this.props.counter.fieldGroupBy,
      referenceOp: event.target.value as QueryFilterNumberOperation,
    };
    this.props.onCounterChange(newCounter);
  };

  render() {
    const itemByName = (name: string) => {
      return this.props.groupByAvailable.find((x) => x.name === name);
    };

    const fieldType: string =
      Immutable.List(this.props.groupBy)
        .map((x) => itemByName(x))
        .filter((x) => !!x)
        .map((x) => x!)
        .filter((x) => x.name === this.props.counter?.field)
        .map((x) => x.descriptor.type)
        .first() ?? "";
    const availableOp = fieldType !== "number" ? ["count"] : valueOpHardcoded;

    //if we use number field we need group by other field!!!
    const needGroupByField = fieldType === "number";

    const numberOpHardcoded: {
      value: QueryFilterNumberOperation;
      label: string;
    }[] = [
      { value: "gt", label: ">" },
      { value: "lt", label: "<" },
      { value: "gte", label: ">=" },
      { value: "lte", label: "<=" },
      { value: "eq", label: "==" },
      { value: "neq", label: "!=" },
    ];

    const withReferenceValue: boolean =
      this.props.counter?.referenceValue !== undefined;

    return (
      <div>
        <Typography variant="h6">Counter</Typography>
        <Typography variant="subtitle2">
          Note: result must contain only 1 item. This can be achived via
          filters.
        </Typography>
        <Typography variant="subtitle2">&nbsp;</Typography>
        <Grid container spacing={2} direction="column" alignItems="stretch">
          <Grid item>
            <TextField
              id="value-outlined-text"
              label={fieldType !== "" ? "Field / " + fieldType : "Field"}
              size="small"
              select
              fullWidth
              value={this.props.counter?.field}
              onChange={this.handleSelectField}
              variant="outlined"
            >
              {this.props.groupBy
                .map((x) => itemByName(x))
                .filter((x) => !!x)
                .map((x) => x!)
                .map((item) => (
                  <MenuItem key={item.name} value={item.name}>
                    {item.translation}
                  </MenuItem>
                ))}
            </TextField>
          </Grid>
          <Grid item>
            <TextField
              id="value-outlined-text"
              label="Value op"
              size="small"
              select
              fullWidth
              value={this.props.counter?.op}
              onChange={this.handleSelectOp}
              variant="outlined"
            >
              {availableOp.map((option) => (
                <MenuItem key={option} value={option}>
                  {option}
                </MenuItem>
              ))}
            </TextField>
          </Grid>

          {needGroupByField ? (
            <Grid item>
              <TextField
                id="value-outlined-text"
                label="Group by"
                size="small"
                select
                fullWidth
                value={this.props.counter?.fieldGroupBy}
                onChange={this.handleSelectFieldGroupBy}
                variant="outlined"
              >
                {this.props.groupBy
                  .map((x) => itemByName(x))
                  .filter((x) => !!x)
                  .map((x) => x!)
                  .filter(
                    (item) =>
                      item.descriptor.type === "set" ||
                      item.descriptor.type === "dictionary"
                  )
                  .map((item) => (
                    <MenuItem key={item.name} value={item.name}>
                      {item.translation}
                    </MenuItem>
                  ))}
              </TextField>
            </Grid>
          ) : null}
          <Grid item>
            <FormControlLabel
              control={
                <Switch
                  checked={withReferenceValue}
                  onChange={this.handleWithReferenceValue}
                  size="small"
                />
              }
              label="Highlight if"
            />
          </Grid>
          {withReferenceValue ? (
            <Grid item>
              <Grid container spacing={2} direction="row" alignItems="stretch">
                <Grid item xs={3} sm={3}>
                  <Typography>Value</Typography>                  
                </Grid>
                <Grid item xs={3} sm={3}>
                  <TextField
                    id="op-value-textfield"
                    label="compare operation"
                    size="small"
                    select
                    fullWidth
                    value={this.props.counter?.referenceOp}
                    onChange={this.handleReferenceOp}
                    variant="outlined"
                  >
                    {numberOpHardcoded.map((op, index) => {
                      return (
                        <MenuItem key={op.value} value={op.value}>
                          {op.label}
                        </MenuItem>
                      );
                    })}
                  </TextField>
                </Grid>
                <Grid item xs={3} sm={3}>
                  <TextField
                    id="reference-outlined-number"
                    label="Reference"
                    size="small"
                    fullWidth
                    value={this.props.counter?.referenceValue}
                    onChange={this.handleReferenceValue}
                    type="number"
                    InputLabelProps={{
                      shrink: true,
                    }}
                    variant="outlined"
                  />
                </Grid>
              </Grid>
            </Grid>
          ) : null}
        </Grid>
      </div>
    );
  }
}

export default VisualizeTypeCounter;
