import * as React from "react";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Grid from "@material-ui/core/Grid";
import * as Immutable from "immutable";
import {
  EventField,
  ChartState,
  EventFieldOp,
  ChartType,
  PivotTableState,
} from "../WidgetEditor/types";
import { FormControlLabel, Switch } from "@material-ui/core";

type Props = {
  bottom?: boolean;
  onMarkdownBottomChange: (bottom: boolean) => void;
};

function VisualizeTypeMarkdown(props: Props) {
  const handleMarkdownBottomChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    console.log("handleMarkdownBottomChange " + event.target.value);
    const x: boolean = event.target.checked;
    props.onMarkdownBottomChange(x);
  };

  return (
    <div title="VisualizeTypePivotTable">
      <Typography variant="h6">Markdown</Typography>
      <Typography variant="subtitle2">&nbsp;</Typography>
      <Grid container spacing={2} direction="column" alignItems="stretch">
        <Grid item>
          <FormControlLabel
            control={
              <Switch
                checked={props.bottom ?? false}
                onChange={handleMarkdownBottomChange}
                size="small"
              />
            }
            label="at bottom"
          />
        </Grid>
      </Grid>
    </div>
  );
}

export default VisualizeTypeMarkdown;
