import * as React from "react";
import { DashboardState } from "../WidgetEditor/types";
import {
  Switch as RouterSwitch,
  Route,
  Link,
  Redirect,
  useParams,
  useLocation,
  useRouteMatch,
} from "react-router-dom";
import DashboardIcon from '@material-ui/icons/Dashboard';
import { Container, Typography } from "@material-ui/core";

type Props = {
  dashboardsJsonUrl: string;
  onLoadedDashboards: (list: DashboardState[]) => void;
};

type State = {
  data?: DashboardState[];
  err?: string;
};

function waitMs<T>(ms: number, value: T) {     
  return new Promise<T>((resolve) => setTimeout(resolve, ms, value));   
}

class LoadingDashboards extends React.Component<Props, State> {
  state: State = {
    data: undefined,
    err: undefined,
  };
  
  componentDidMount() {
    fetch(this.props.dashboardsJsonUrl)      
      .then((response) => response.json())      
      .then(value => waitMs(2000, value))
      .then((data) => {
        if(data.length > 0){
          this.props.onLoadedDashboards(data);
          this.setState({ data: data });
        } else {
          this.setState({ err: "No dashboards found in " + this.props.dashboardsJsonUrl });
        }        
      })
      .catch((e) => {
        this.setState({ err: e.message });
      });
  }

  render() {
    if (this.state.err) {
      return (
        <div>
          <Container>
            <Typography>Error: {this.state.err}</Typography>
            <Typography><Link to="/">Return to dashboards</Link></Typography>
          </Container>
        </div>
      );
    }

    if (this.state.data) {
      return <Redirect to="/" />;
    }

    return (
      <div style={{
        height: '90vh', margin: 0, padding: 0,
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
      }}>
        <DashboardIcon/>&nbsp;&nbsp;Loading dashboard from {this.props.dashboardsJsonUrl}...
      </div>
      // <div>
      //   <Container>
      //     <Typography>
      //       Loading dashboard from {this.props.dashboardsJsonUrl} ...
      //     </Typography>
      //   </Container>
      // </div>
    );
  }
}

export default LoadingDashboards;
