import React, { forwardRef } from "react";
// import { makeStyles } from "@material-ui/core/styles";
import MaterialTable, { MTableCell } from "material-table";
import AddBox from "@material-ui/icons/AddBox";
import ArrowDownward from "@material-ui/icons/ArrowDownward";
import Check from "@material-ui/icons/Check";
import ChevronLeft from "@material-ui/icons/ChevronLeft";
import ChevronRight from "@material-ui/icons/ChevronRight";
import Clear from "@material-ui/icons/Clear";
import DeleteOutline from "@material-ui/icons/DeleteOutline";
import Edit from "@material-ui/icons/Edit";
import FilterList from "@material-ui/icons/FilterList";
import FirstPage from "@material-ui/icons/FirstPage";
import LastPage from "@material-ui/icons/LastPage";
import Remove from "@material-ui/icons/Remove";
import SaveAlt from "@material-ui/icons/SaveAlt";
import Search from "@material-ui/icons/Search";
import FullscreenIcon from "@material-ui/icons/Fullscreen";
import CloseIcon from "@material-ui/icons/Close";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import ViewColumn from "@material-ui/icons/ViewColumn";
// import sampleData from "./agg__time.hour__event.detector.People.state.json";
import {
  TableState,
  QueryFilterKey,
  TimePeriod,
  PossibleValues,
  PivotTableState,
  EventField,
  PossibleValueDictionary,
} from "../WidgetEditor/types";
import * as Immutable from "immutable";
import {
  Card,
  CardContent,
  CardHeader,
  Typography,
  IconButton,
  DialogContent,
  DialogTitle,
  Dialog,
  Toolbar,
  Container,
  CardActions,
  Paper,
} from "@material-ui/core";
import moment from "moment";

// const useStyles = makeStyles({
//   table: {
//     minWidth: 650,
//   },
// });

const icon = (MatIcon: any) =>
  forwardRef<SVGSVGElement>((props, ref) => <MatIcon {...props} ref={ref} />);

const tableIcons = {
  Add: icon(AddBox),
  Check: icon(Check),
  Clear: icon(Clear),
  Delete: icon(DeleteOutline),
  DetailPanel: icon(ChevronRight),
  Edit: icon(Edit),
  Export: icon(SaveAlt),
  Filter: icon(FilterList),
  FirstPage: icon(FirstPage),
  LastPage: icon(LastPage),
  NextPage: icon(ChevronRight),
  PreviousPage: icon(ChevronLeft),
  ResetSearch: icon(Clear),
  Search: icon(Search),
  SortArrow: icon(ArrowDownward),
  ThirdStateCheck: icon(Remove),
  ViewColumn: icon(ViewColumn),
};

type Props = {
  isEditMode: boolean;
  api: string;
  token: string;
  refreshTime?: Date;

  id: string;
  title?: string;
  description?: string;
  tableState: PivotTableState;
  filterItems: Immutable.List<QueryFilterKey>;
  period: TimePeriod;
  possibleValues: PossibleValues;
  groupByAvailable: EventField[];

  onEditWidget?: ((id: string) => void) | undefined;
  onDeleteWidget?: ((id: string) => void) | undefined;
};

type State = {
  rows?: any;
  err?: string;
  openFullscreen: boolean;
  selected?: string;
};

class WidgetPivotTable extends React.Component<Props, State> {
  state: State = {
    rows: undefined,
    err: undefined,
    openFullscreen: false,
    selected: undefined,
  };

  handleFullscreenOpen = () => {
    this.setState((prevState) => {
      return {
        ...prevState,
        openFullscreen: true,
      };
    });
  };

  handleFullscreenClose = () => {
    this.setState((prevState) => {
      return {
        ...prevState,
        openFullscreen: false,
      };
    });
  };

  handleEdit = () => {
    console.log("handleEdit " + this.props.id);
    if (this.props.onEditWidget) {
      this.props.onEditWidget(this.props.id);
    }
  };
  handleDelete = () => {
    console.log("handleDelete " + this.props.id);
    if (this.props.onDeleteWidget) {
      this.props.onDeleteWidget(this.props.id);
    }
  };

  loadData = () => {
    console.log("WidgetPivotTable loadData");
    this.setState({ rows: undefined, err: undefined });
    //Simple POST request with a JSON body using fetch
    const url1 = this.props.api + "/api/v2/ad-query/aggregation/table";

    const { rows, columns, values, op } = this.props.tableState;

    const d = {
      filter: {
        keys: this.props.filterItems.filter(
          (x) => x.type === "set" || x.type === "dictionary"
        ),
        numbers: this.props.filterItems
          .filter((x) => x.type === "number")
          .map((x) => {
            return {
              field: x.field,
              op: x.op,
              value: Number.parseFloat(x.value),
            };
          }),
        lists: [],
        period: this.props.period,
      },
      groupBy: [rows, columns, values],
      fields: [
        {
          field: values,
          op: op,
        },
      ],
    };
    const requestOptions: RequestInit = {
      method: "POST",
      //mode: "no-cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.props.token,
      },
      body: JSON.stringify(d), //JSON.stringify({ title: 'React POST Request Example' })
    };
    fetch(url1, requestOptions)
      //.then(r => r.json().then(json => ({ok: r.ok, status: r.status, json})))
      //.then( r => r.ok ? r.json: Promise.reject(r))
      .then((response) => response.json())
      .then((data) => {
        //this.props.onChangeEventFields(data);
        if (data.result == null) {
          throw Error("no rows in result" + JSON.stringify(data));
        } else {
          const r = data.result.map((item: any) => {
            return item;
          });
          this.setState({ rows: r });
        }
        console.log("WidgetPivotTable data=" + JSON.stringify(data));
      })
      .catch((e) => {
        this.setState({ err: e.message });
      });
  };
  componentDidMount() {
    console.log("WidgetPivotTable componentDidMount ");
    this.loadData();
  }

  componentDidUpdate(prevProps: Props) {
    console.log("WidgetPivotTable componentDidUpdate ");
    if (JSON.stringify(this.props) !== JSON.stringify(prevProps)) {
      // Check if it's a new user, you can also use some unique property, like the ID  (this.props.user.id !== prevProps.user.id)
      this.loadData();
    }
  }

  render() {
    const { rows, columns, values, op } = this.props.tableState;

    if (!rows || !columns || !op || !values) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>
              Error: state is not complete, content{" "}
              {JSON.stringify(this.props.tableState!)}
            </Typography>
          </CardContent>
        </Card>
      );
    }

    if (this.state.err) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>Error: {this.state.err}</Typography>
          </CardContent>
        </Card>
      );
    }

    if (!this.state.rows) {
      return (
        <Card variant="outlined">
          <CardContent>
            <Typography>No rows</Typography>
          </CardContent>
        </Card>
      );
    }

    const data = this.state.rows!;

    const columnsField = this.props.groupByAvailable.filter(
      (xx) => xx.name === columns
    );
    const columnsFieldName: string = columns;
    const columnsFieldType: string = columnsField[0].descriptor.type;

    const rowsField = this.props.groupByAvailable.filter(
      (xx) => xx.name === rows
    );
    const rowsFieldName: string = rows;
    const rowsFieldType: string = rowsField[0].descriptor.type;
    const rowsFieldTranslation: string = rowsField[0].translation;

    var columnsValues: PossibleValueDictionary[] = [];
    if (columnsFieldType === "set") {
      columnsValues =
        this.props.possibleValues.set.get(columnsFieldName)?.map((xx) => {
          return {
            key: xx,
            value: xx,
          };
        }) || [];
    } else if (columnsFieldType === "dictionary") {
      columnsValues =
        this.props.possibleValues.dictionary.get(columnsFieldName) || [];
      columnsValues = columnsValues.map((xx) => {
        return {
          key: xx.key,
          value: xx.value && xx.value !== "" ? xx.value : xx.key,
        };
      });
    }

    console.log("columnsValues = " + JSON.stringify(columnsValues));

    var rowsValues: PossibleValueDictionary[] = [];
    if (rowsFieldType === "set") {
      rowsValues =
        this.props.possibleValues.set.get(rowsFieldName)?.map((xx) => {
          return {
            key: xx,
            value: xx,
          };
        }) || [];
    } else if (rowsFieldType === "dictionary") {
      rowsValues =
        this.props.possibleValues.dictionary.get(rowsFieldName) || [];
      rowsValues = rowsValues.map((xx) => {
        return {
          key: xx.key,
          value: xx.value && xx.value !== "" ? xx.value : xx.key,
        };
      });
    }

    console.log("rowsValues = " + JSON.stringify(rowsValues));

    //filter to series in data
    const columnsInDataValues: Set<string> = data.reduce(
      (coll: any, item: any) => {
        const s: string = item[columns];
        coll.add(s);
        return coll;
      },
      new Set()
    );
    const rowsInDataValues: Set<string> = data.reduce(
      (coll: any, item: any) => {
        const s: string = item[rows];
        coll.add(s);
        return coll;
      },
      new Set()
    );

    console.log("columnsInDataValues = " + JSON.stringify(columnsInDataValues));
    console.log("rowsInDataValues = " + JSON.stringify(rowsInDataValues));

    columnsValues = columnsValues.filter((sv) => {
      return columnsInDataValues.has(sv.key);
    });
    rowsValues = rowsValues.filter((sv) => {
      return rowsInDataValues.has(sv.key);
    });

    console.log("columnsValues = " + JSON.stringify(columnsValues));
    console.log("rowsValues = " + JSON.stringify(rowsValues));

    const v = data.reduce((coll: any, item: any) => {
      const currentX = item[rows];
      const valueForX = coll.get(currentX) || { x: currentX };

      valueForX[item[columns]] = item[op + "_" + values];
      coll.set(currentX, valueForX);

      return coll;
    }, new Map());

    console.log(v);

    

    const vv = Immutable.List(v.values()).sortBy((i: any) => i.x);

    var dd: any[] = vv.toArray();
    console.log("dd = " + JSON.stringify(dd));

    const withDelta = this.props.tableState.delta && this.props.tableState.deltaA && this.props.tableState.deltaB;
    const deltaTitle = this.props.tableState.deltaTitle ?? "";
    const onlyDeltaColumn = withDelta && this.props.tableState.deltaOnly;

    if(withDelta) {
      const deltaA = this.props.tableState.deltaA!;
      const deltaB = this.props.tableState.deltaB!;
      
      const vvv = Immutable.List(dd).map((item: any) => {
        const a = Number(item[deltaA]);
        const b = Number(item[deltaB]);
        
        const value = item;
        value[deltaTitle] = a - b;
        return value;
      });

      dd = vvv.toArray();
      console.log("dd with delta = " + JSON.stringify(dd));
    }

    if(this.props.tableState.referenceValue && this.props.tableState.referenceOp) {
      const referenceColumn = this.props.tableState.referenceColumn ?? "*";
      const referenceValue = this.props.tableState.referenceValue!;
      const referenceOp = this.props.tableState.referenceOp!;

      const vvv = Immutable.List(dd).map((item: any) => {
        const value = Number(item[referenceColumn]);
        var highlight = false;
        switch (referenceOp) {
          case "eq":
            highlight = value === referenceValue;
            break;
          case "neq":
            highlight = value !== referenceValue;
            break;
          case "gt":
            highlight = value > referenceValue;
            break;
          case "lt":
            highlight = value < referenceValue;
            break;
          case "gte":
            highlight = value >= referenceValue;
            break;
          case "lte":
            highlight = value <= referenceValue;
            break;
          default:
            break;
        }

        const res = item;
        res["highlight"] = highlight;
        return res;
      });

      dd = vvv.toArray();
      console.log("dd with highlight = " + JSON.stringify(dd));
    }

    // const columns = this.props.tableState.order.map((item) => {
    //   return {
    //     field: item,
    //     title: item,
    //   };
    // });

    const rowsLookup = rowsValues.reduce((coll: any, item: any) => {
      const currentX = item.key;
      const valueForX = item.value;
      coll.set(currentX, valueForX);
      return coll;
    }, new Map());
    const rowsLookupObject = Object.fromEntries(rowsLookup.entries());
    console.log(rowsLookupObject);

    const xColumn: any = {
      field: "x",
      title: rowsFieldTranslation,
      lookup: rowsFieldType === "dictionary" ? rowsLookupObject : undefined,
    };
    const tableColumns: any[] = columnsValues.map((item) => {
      return {
        field: item.key,
        title: item.value,
      };
    });
    const deltaColumn: any = {
      field: deltaTitle,
      title: deltaTitle,
    };
    const highlightColumn: any = {
      field: "highlight",
      title: "highlight",
      hidden: true
    };
    const allColumns = [xColumn].
      concat(onlyDeltaColumn ? []: tableColumns)
      .concat(withDelta ? deltaColumn : [])
      .concat([highlightColumn]);
    const tableRows = dd;

    const theTable = (
      <MaterialTable
        icons={tableIcons}
        columns={allColumns}
        // localization={{
        //   toolbar: { exportName: "Export" },
        // }}
        onColumnDragged={(sourceIndex: number, destinationIndex: number) => {
          console.log(
            "Dropped column from " +
              sourceIndex +
              " to position " +
              destinationIndex
          );
        }}
        options={{
          showTitle: false,
          padding: "dense",
          exportAllData: true,
          exportButton: true,
          exportFileName:
            this.props.title + "." + moment().format("YYYYMMDDTHHmmss"),
          fixedColumns: {
            left: this.props.tableState.fixed1 ? 1 : 0,
            right: 0,
          },
          // exportCsv: (columns, data) => {
          //   alert(
          //     "You should develop a code to export " + data.length + " rows"
          //   );
          // },
          rowStyle: (rowData) => {
            //console.log("rowStyle " + JSON.stringify(rowData));
            return {
              backgroundColor:
              this.state.selected === rowData.tableData.id ? "#EEE" : "#FFF",
            color:
              rowData.highlight ? "#F00" : "#000",
            };            
          },
        }}
        onRowClick={(evt, selectedRow: any) =>
          this.setState({ selected: selectedRow?.tableData.id })
        }
        //title={this.props.title}
        title={
          <Container>
            <Typography variant="h5" component="h5">
              {this.props.title}
            </Typography>
            <Typography color="textSecondary">
              {this.props.description}
            </Typography>
          </Container>
        }
        isLoading={!this.state.rows && !this.state.err}
        data={tableRows}
        components={{
          Container: (props) => <Paper {...props} elevation={0} />,
          //doesn't work
          // Cell: props => {
          //   console.log("MTableCell " + props.value + " > 100 = " + (props.value > 100));
          //   return (
          //     <MTableCell
          //       style={{
          //         color:
          //           props.value > 100 // For the specific value
          //             ? "#0FF"
          //             : "white"
          //       }}
          //       {...props}
          //     />
          //   );
          // }
        }}
      />
    );

    return (
      <div id={this.props.id}>
        <Card variant="outlined" title={this.props.title}>
          <CardHeader
            action={
              <IconButton
                aria-label="fullscreen"
                onClick={this.handleFullscreenOpen}
              >
                <FullscreenIcon />
              </IconButton>
              // <IconButton aria-label="settings">
              //   <MoreVertIcon />
              // </IconButton>
            }
            title={this.props.title}
            subheader={this.props.description}
          />
          <CardContent>{theTable}</CardContent>
          {this.props.isEditMode ? (
            <CardActions disableSpacing>
              <IconButton aria-label="edit" onClick={this.handleEdit}>
                <EditIcon />
              </IconButton>
              <IconButton aria-label="delete" onClick={this.handleDelete}>
                <DeleteIcon />
              </IconButton>
            </CardActions>
          ) : null}
        </Card>
        <Dialog
          fullScreen
          open={this.state.openFullscreen}
          onClose={this.handleFullscreenClose}
        >
          <DialogTitle id="editor-dialog-title">
            <Toolbar variant="dense">
              <IconButton
                edge="start"
                color="inherit"
                onClick={this.handleFullscreenClose}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
              <Typography variant="h6">{this.props.title}</Typography>
              {/* <Button color="inherit" onClick={this.handleDownloadPdf}>
                PDF
              </Button>
              <Button color="inherit" onClick={this.handleDownloadSvg}>
                SVG
              </Button>
              <Button color="inherit" onClick={this.handleDownloadPng}>
                PNG
              </Button> */}
            </Toolbar>
          </DialogTitle>
          <DialogContent>
            <Typography variant="h6">{this.props.description}</Typography>
            {theTable}
          </DialogContent>
        </Dialog>
      </div>
    );
  }
}

export default WidgetPivotTable;
