import * as React from "react";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Grid from "@material-ui/core/Grid";
import * as Immutable from "immutable";
import { EventField, ChartState, EventFieldOp } from "../WidgetEditor/types";

type VisualizeTypePieProps = {
  chart?: ChartState;
  groupBy: Immutable.List<string>;
  groupByAvailable: EventField[];
  onChartChange: (chart: ChartState) => void;
};

function VisualizeTypePie(props: VisualizeTypePieProps) {
  const valueOpHardcoded: EventFieldOp[] = [
    "count",
    "avg",
    "sum",
    "min",
    "max",
  ];

  const itemByName = (name: string) => {
    return props.groupByAvailable.find((x) => x.name === name)
  };

  const handleSelectX = (event: React.ChangeEvent<HTMLInputElement>) => {
    //setName(event.target.value);
    console.log("handleSelectX " + event.target.value);
    const item = props.groupBy.map((x) => itemByName(x)).filter((x) => x != null).map((x)=> x!).find((x) => x.name === event.target.value);
    console.log("handleSelectX item " + item?.name);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newChart = { ...props.chart, x: newValue };
    props.onChartChange(newChart);
  };

  const handleSelectY = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectY " + event.target.value);
    const item = props.groupBy.map((x) => itemByName(x)).filter((x) => x != null).map((x)=> x!).find((x) => x.name === event.target.value);
    console.log("handleSelectY item " + item?.name);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newOp = item?.descriptor.type !== "number" ? "count" : props.chart?.op;
    const newChart = { ...props.chart, y: newValue, op: newOp };
    props.onChartChange(newChart);
  };

  const handleSelectYOp = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectYOp " + event.target.value);
    const newChart = { ...props.chart, op: event.target.value as EventFieldOp };
    props.onChartChange(newChart);
  };

  const handleSelectSerie = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectSerie " + event.target.value);
    const item = props.groupBy.map((x) => itemByName(x)).filter((x) => x != null).map((x)=> x!).find((x) => x.name === event.target.value);
    console.log("handleSelectSerie item " + item?.name);
    const newValue =
      event.target.value && event.target.value !== ""
        ? event.target.value
        : undefined;
    const newChart = { ...props.chart, series: newValue };
    props.onChartChange(newChart);
  };

  const availableFieldsX = props.groupBy.map((x) => itemByName(x)).filter((x) => x != null).map((x)=> x!).filter(
    (x) => !(x.name === props.chart?.y || x.name === props.chart?.series)
  );
  const availableFieldsY = props.groupBy.map((x) => itemByName(x)).filter((x) => x != null).map((x)=> x!).filter(
    (x) => !(x.name === props.chart?.x || x.name === props.chart?.series)
  );
  const availableFieldsS = props.groupBy.map((x) => itemByName(x)).filter((x) => x != null).map((x)=> x!).filter(
    (x) => !(x.name === props.chart?.x || x.name === props.chart?.y)
  );

  const groupByItems = Immutable.List(props.groupBy)
    .map((x) => itemByName(x)).filter((x) => !!x).map((x)=> x!)
  const xType: string =
    groupByItems
      .filter((x) => x.name === props.chart?.x)
      .map((x) => x.descriptor.type)
      .first() ?? "";
  const yType: string =
    groupByItems
      .filter((x) => x.name === props.chart?.y)
      .map((x) => x.descriptor.type)
      .first() ?? "";
  const sType: string =
    groupByItems
      .filter((x) => x.name === props.chart?.series)
      .map((x) => x.descriptor.type)
      .first() ?? "";

  const availableOp = yType !== "number" ? ["count"] : valueOpHardcoded;

  return (
    <div title="VisualizeType">
      <Typography variant="h6">Pie</Typography>
      <Typography variant="subtitle2">&nbsp;</Typography>
      <Grid container spacing={2} direction="column" alignItems="stretch">
        <Grid item>
          <TextField
            id="x-outlined-text"
            label={xType !== "" ? ("Name Field / " + xType) : "Name Field"}
            size="small"
            select
            fullWidth
            value={props.chart?.x ?? ""}
            onChange={handleSelectX}
            variant="outlined"
          >
            <MenuItem key="[no value]" value="">
              [ No value ]
            </MenuItem>
            {availableFieldsX.map((item) => (
              <MenuItem key={item.name} value={item.name}>
                {item.translation}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item>
          <TextField
            id="y-outlined-text"
            label={yType !== "" ? ("Value Field / " + yType) : "Value Field"}
            size="small"
            select
            fullWidth
            value={props.chart?.y ?? ""}
            onChange={handleSelectY}
            variant="outlined"
          >
            <MenuItem key="[no value]" value="">
              [ No value ]
            </MenuItem>
            {availableFieldsY.map((item) => (
              <MenuItem key={item.name} value={item.name}>
                {item.translation}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item>
          <TextField
            id="value-outlined-text"
            label="Value op"
            size="small"
            select
            fullWidth
            value={props.chart?.op}
            onChange={handleSelectYOp}
            variant="outlined"
          >
            {availableOp.map((option) => (
              <MenuItem key={option} value={option}>
                {option}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item>
          <TextField
            id="series-outlined-text"
            label={
              sType !== "" ? ("Series Field / " + sType) : "Series Field"
            }
            size="small"
            select
            fullWidth
            value={props.chart?.series ?? ""}
            onChange={handleSelectSerie}
            variant="outlined"
          >
            <MenuItem key="[no value]" value="">
              [ No value ]
            </MenuItem>
            {availableFieldsS.map((item) => (
              <MenuItem key={item.name} value={item.name}>
                {item.translation}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
      </Grid>
    </div>
  );
}

export default VisualizeTypePie;
