import * as React from "react";
import * as Immutable from "immutable";
import { Link } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import { Lang, WidgetEditorInternalState, WidgetEditorRealState, PossibleValues, EventField, QueryFilterKey, TimePeriod } from "../WidgetEditor/types";
import { Card, CardContent, Typography, Tooltip } from "@material-ui/core";
import WidgetEditorLoadingEventFields from "../../components/WidgetEditor/WidgetEditorLoadingEventFields";
import WidgetEditorLoadingPossibleValues from "../../components/WidgetEditor/WidgetEditorLoadingPossibleValues";
import WidgetMarkdown from "../WidgetPreview/WidgetMarkdown";
import WidgetAggregationTable from "../WidgetPreview/WidgetAggregationTable";
import WidgetPivotTable from "../WidgetPreview/WidgetPivotTable";
import WidgetEvents from "../WidgetPreview/WidgetEvents";
import WidgetPreviewPie from "../WidgetPreview/WidgetPreviewPie";
import WidgetCounter from "../WidgetPreview/WidgetCounter";
import WidgetPreviewChart from "../WidgetPreview/WidgetPreviewChart";
import QueryEditorTimeperiod from "../QueryEditor/QueryEditorTimeperiod";
import QueryEditorFilter from "../QueryEditor/QueryEditorFilter";
import WidgetEditorLoadingPossiblePeriods from "../WidgetEditor/WidgetEditorLoadingPossiblePeriods";
import AddCircleOutlineOutlinedIcon from "@material-ui/icons/AddCircleOutlineOutlined";
import MenuIcon from "@material-ui/icons/Menu";
import EditIcon from "@material-ui/icons/Edit";

type DashboardProps = {
  id: string;
  api: string;
  token: string;
  lang: Lang;
  isCommonFilterEnabled: boolean;
  isEditMode: boolean;
  refreshTime: Date;

  widgets: WidgetEditorRealState[];
  internal: WidgetEditorInternalState;

  onLoadEventFields: (values: EventField[]) => void;
  onLoadPossibleValues: (values: PossibleValues) => void;
  onLoadPossiblePeriods: (values: TimePeriod[]) => void;

  onEditWidget: ((id: string) => void) | undefined;
  onDeleteWidget: ((id: string) => void) | undefined;
};

type DashboardState = {
  filterItems: Immutable.List<QueryFilterKey>;
  period: TimePeriod;
};

class Dashboard extends React.Component<DashboardProps, DashboardState> {

  state: DashboardState = {
    filterItems: Immutable.List([]),
    period: {
      type: "forever",
    },
  }
  handleAddFilterItem = (item: QueryFilterKey) => {
    console.log("Dashboard handleAddFilterItem " + JSON.stringify(item));
    this.setState((prevState) => ({ 
      ...prevState, 
      filterItems: Immutable.List(prevState.filterItems).push(item)
    }));
  };
  handleRemoveFilterItem = (item: QueryFilterKey) => {
    console.log("Dashboard handleRemoveFilterItem " + JSON.stringify(item));
    this.setState((prevState) => ({ 
      ...prevState, 
      filterItems: prevState.filterItems.filter(
        (x) => JSON.stringify(x) !== JSON.stringify(item)
      )
    }));
  };
  handleTimeperiod = (item: TimePeriod) => {
    console.log("Dashboard handleTimeperiod " + item.type);
    this.setState((prevState) => ({ 
      ...prevState, 
      period: { type: item.type }
    }));
  };
  
  render() {
    if (this.props.widgets.length === 0) {
      return ( 
        <Card variant="outlined">
          <CardContent>
            <Typography>No widgets created</Typography>
            <Typography>Use <MenuIcon /> button to select dashboard</Typography>
            <Typography>Or turn on edit mode with <EditIcon/> and use <AddCircleOutlineOutlinedIcon /> button to add widget</Typography>
            <Typography>&nbsp;</Typography>
            <Typography>If something is unclear use <Link to={"/dashboard/11111111-1111-1111-1111-111111111111"}>Tutorial</Link> </Typography>
            <Typography>&nbsp;</Typography>
            <Typography>Dashboards can be loaded from external file url, in url encoded format</Typography>
            <Typography>Example: "#/inbox/dashboard/file/https%3A%2F%2Fraw.githubusercontent.com%2Favgx%2Fadd%2Fgh-pages%2Fdashboards.json"</Typography>
            <Typography>
              <Link to={"/inbox/dashboard/file/https%3A%2F%2Fraw.githubusercontent.com%2Favgx%2Fadd%2Fgh-pages%2Fdashboards.json"}>Click this link to see an example</Link> 
            </Typography>
          </CardContent>
        </Card>
      );
    }

    if (!this.props.internal.possiblePeriodsLoaded) {
      return (
        <WidgetEditorLoadingPossiblePeriods
          api={this.props.api}
          token={this.props.token}
          lang={this.props.lang}
          onLoadPossiblePeriods={this.props.onLoadPossiblePeriods}
        />
      );
    }
    
    if (!this.props.internal.groupByAvailableLoaded) {
      return (
        <WidgetEditorLoadingEventFields
          api={this.props.api}
          token={this.props.token}
          lang={this.props.lang}
          onChangeEventFields={this.props.onLoadEventFields}
        />
      );
    }

    if (!this.props.internal.possibleValuesLoaded) {
      return (
        <WidgetEditorLoadingPossibleValues
          api={this.props.api}
          token={this.props.token}
          eventFields={this.props.internal.groupByAvailable}
          onChangePossibleValues={this.props.onLoadPossibleValues}
        />
      );
    }

    console.log("Dashboard widgets: " + JSON.stringify(this.props.widgets, undefined, 2));

    const markdownTopWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "Markdown" && !x.bottom);
    const markdownBottomWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "Markdown" && x.bottom);
    const chartWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "Chart");
    const pieWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "Pie");
    const counterWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "Counter");
    const tableWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "Table");
    const pivotWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "PivotTable");
    const eventsWidgets = Immutable.List(this.props.widgets).filter((x) => x.visualizeType === "Events");

    return (
      <div>
        {/* <Card variant="outlined">
          <CardContent>
            <Typography>Dashboard with {this.props.widgets.length} widgets</Typography>
          </CardContent>
        </Card> */}
        
        <Typography variant="subtitle2">&nbsp;</Typography>
        {this.props.isCommonFilterEnabled ? (
          <Tooltip title="Dashboard common filter that will be applied to every widget">
          <Grid container spacing={2} direction="row" alignItems="stretch" alignContent="center">
            <Grid item>
              <QueryEditorTimeperiod
                period={this.state.period}
                possiblePeriods={this.props.internal.possiblePeriods}
                onTimeperiod={this.handleTimeperiod}
              />
            </Grid>          
            <Grid item>
              <QueryEditorFilter
                filterItems={this.state.filterItems}
                possibleValues={this.props.internal.possibleValues}
                groupByAvailable={this.props.internal.groupByAvailable}
                onAddFilterItem={this.handleAddFilterItem}
                onRemoveFilterItem={this.handleRemoveFilterItem}
              />
            </Grid>
          </Grid>
          </Tooltip>
        ) : null }
        
        <Grid container spacing={2} alignItems="stretch">
        {markdownTopWidgets.map((item) => (
          <Grid item xs={12}>
            <WidgetMarkdown
              isEditMode={this.props.isEditMode}
              id={item.id}
              title={item.title}
              description={item.description} 
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}    
            />
          </Grid>
        ))}
        </Grid>
        <Grid container spacing={2} alignItems="stretch">
        {counterWidgets.map((item) => (
          <Grid item xs={12} sm={3}>
            <WidgetCounter
              isEditMode={this.props.isEditMode}
              api={this.props.api}
              token={this.props.token}
              id={item.id}
              title={item.title}
              description={item.description}
              counterState={item.counter!}
              filterItems={this.props.isCommonFilterEnabled ? Immutable.List(item.filterItems).concat(this.state.filterItems) : item.filterItems}
              period={this.props.isCommonFilterEnabled ? this.state.period : item.period}
              refreshTime={this.props.refreshTime}
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}              
            />
          </Grid>
        ))}
        </Grid>
        <Grid container spacing={2} alignItems="stretch">
        {chartWidgets.map((item) => (
          <Grid item xs={12} sm={6}>
            <WidgetPreviewChart 
              isEditMode={this.props.isEditMode}
              api={this.props.api}
              token={this.props.token}
              id={item.id}
              title={item.title}
              description={item.description}
              chartState={item.chart!}
              filterItems={this.props.isCommonFilterEnabled ? Immutable.List(item.filterItems).concat(this.state.filterItems) : item.filterItems}
              period={this.props.isCommonFilterEnabled ? this.state.period : item.period}
              possibleValues={this.props.internal.possibleValues}
              groupByAvailable={this.props.internal.groupByAvailable} 
              refreshTime={this.props.refreshTime}
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}    
            />
          </Grid>
        ))}        
        {pieWidgets.map((item) => (
          <Grid item xs={12} sm={6}>
            <WidgetPreviewPie
              isEditMode={this.props.isEditMode}
              api={this.props.api}
              token={this.props.token}
              id={item.id}
              title={item.title}
              description={item.description}
              chartState={item.chart!}
              filterItems={this.props.isCommonFilterEnabled ? Immutable.List(item.filterItems).concat(this.state.filterItems) : item.filterItems}
              period={this.props.isCommonFilterEnabled ? this.state.period : item.period}
              possibleValues={this.props.internal.possibleValues}
              groupByAvailable={this.props.internal.groupByAvailable} 
              refreshTime={this.props.refreshTime}
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}    
            />
          </Grid>
        ))}
        </Grid>
        <Typography variant="subtitle2">&nbsp;</Typography>
        <Grid container spacing={2} alignItems="stretch">
        {tableWidgets.map((item) => (
          <Grid item xs={12}>
            <WidgetAggregationTable
              isEditMode={this.props.isEditMode}
              api={this.props.api}
              token={this.props.token}
              id={item.id}
              title={item.title}
              description={item.description}
              tableState={item.table!}
              groupBy={item.groupBy}
              groupByAvailable={this.props.internal.groupByAvailable}
              filterItems={this.props.isCommonFilterEnabled ? Immutable.List(item.filterItems).concat(this.state.filterItems) : item.filterItems}
              period={this.props.isCommonFilterEnabled ? this.state.period : item.period}
              possibleValues={this.props.internal.possibleValues}
              refreshTime={this.props.refreshTime}
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}    
            />
          </Grid>
        ))}
        {pivotWidgets.map((item) => (
          <Grid item xs={12}>
            <WidgetPivotTable
              isEditMode={this.props.isEditMode}
              api={this.props.api}
              token={this.props.token}
              id={item.id}
              title={item.title}
              description={item.description}
              tableState={item.pivot!}
              filterItems={this.props.isCommonFilterEnabled ? Immutable.List(item.filterItems).concat(this.state.filterItems) : item.filterItems}
              period={this.props.isCommonFilterEnabled ? this.state.period : item.period}
              possibleValues={this.props.internal.possibleValues}
              groupByAvailable={this.props.internal.groupByAvailable} 
              refreshTime={this.props.refreshTime}
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}    
            />
          </Grid>
        ))}
        {eventsWidgets.map((item) => (
          <Grid item xs={12}>
            <WidgetEvents
              isEditMode={this.props.isEditMode}
              api={this.props.api}
              token={this.props.token}
              limit={200}
              id={item.id}
              dashboardId={this.props.id}
              title={item.title}
              description={item.description}
              filterItems={this.props.isCommonFilterEnabled ? Immutable.List(item.filterItems).concat(this.state.filterItems) : item.filterItems}
              period={this.props.isCommonFilterEnabled ? this.state.period : item.period}
              possibleValues={this.props.internal.possibleValues}
              refreshTime={this.props.refreshTime}
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}    
            />
          </Grid>
        ))}
        </Grid>
        <Grid container spacing={2} alignItems="stretch">
        {markdownBottomWidgets.map((item) => (
          <Grid item xs={12}>
            <WidgetMarkdown
              isEditMode={this.props.isEditMode}
              id={item.id}
              title={item.title}
              description={item.description} 
              onEditWidget={this.props.onEditWidget}
              onDeleteWidget={this.props.onDeleteWidget}    
            />
          </Grid>
        ))}
        </Grid>
      </div>
    );
  }
}

export default Dashboard;