const reorder = (
  list: string[],
  startIndex: number,
  endIndex: number
): string[] => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

const sortByArray = (
  list: Immutable.List<EventField>,
  order: string[]
): Immutable.List<EventField> => {
  const res = list.sort((a: EventField, b: EventField) => {
    const aOrder: number = order.findIndex((x) => x === a.name);
    const bOrder: number = order.findIndex((x) => x === b.name);

    if (bOrder === -1 && aOrder === -1) {
      return a.translation < b.translation
        ? -1
        : a.translation > b.translation
        ? 1
        : 0;
    }

    if (aOrder === -1) {
      return 1;
    }

    if (bOrder === -1) {
      return -1;
    }

    if (aOrder < bOrder) {
      return -1;
    }
    if (bOrder < aOrder) {
      return 1;
    }

    return 0;
  });
  return res;
};

