type VisualizeTypeTableProps = {
  table?: TableState;
  groupBy: Immutable.List<string>;
  groupByAvailable: EventField[];
  onTableChange: (table: TableState) => void;
};

const valueOpHardcoded: EventFieldOp[] = ["count", "avg", "sum", "min", "max"];

class VisualizeTypeTable extends React.Component<VisualizeTypeTableProps> {
  handleSelectField = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectField " + event.target.value);
    const newTable: TableState = {
      ...this.props.table,
      field: event.target.value,
      // op: this.props.table?.op ?? "count",
      // order: this.props.table?.order ?? this.props.groupBy.toArray(),
      // visible: [],
    };
    this.props.onTableChange(newTable);
  };

  handleSelectOp = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleSelectYOp " + event.target.value);
    const newTable: TableState = {
      ...this.props.table,
      // field: this.props.table.field,
      op: event.target.value as EventFieldOp,
      // order: this.props.table?.order ?? this.props.groupBy.toArray(),
      // visible: [],
    };
    this.props.onTableChange(newTable);
  };

  handleAggregationFieldInGroupBy = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleAggregationFieldInGroupBy " + event.target.value);
    const x: boolean = event.target.checked;
    const newTable: TableState = {
      ...this.props.table,
      isAggregationFieldInGroupBy: x
    };
    this.props.onTableChange(newTable);
  };

  render() {
    const itemByName = (name: string) => {
      return this.props.groupByAvailable.find((x) => x.name === name);
    };

    const groupByItems = Immutable.List(this.props.groupBy)
      .map((x) => itemByName(x))
      .filter((x) => !!x)
      .map((x) => x!);

    // const sortedGroupBy = sortByArray(
    //   groupByItems,
    //   this.props.table?.order ?? []
    // );

    const aggregateByField = this.props.table?.field ?? Immutable.List(this.props.groupBy).first() ?? "";

    const aggregateByFieldType: string =
      groupByItems
        .filter((x) => x.name === aggregateByField)
        .map((x) => x.descriptor.type)
        .first() ?? "";
    const availableOp = aggregateByFieldType !== "number" ? ["count"] : valueOpHardcoded;
    const currentOpAvailable = availableOp.includes(this.props.table?.op ?? "");
    const aggregateByFieldOp: EventFieldOp = currentOpAvailable  ? (this.props.table?.op ?? "count") : "count";

    return (
      <div title="VisualizeTypeTable">
        <Typography variant="h6">Group by</Typography>
        <Grid container spacing={2} direction="column" alignItems="stretch">
          <Grid item>
            <List>              
              {groupByItems.map((item) => (
                <ListItem
                  dense
                  key={item.name}
                >              
                  <ListItemText primary={item.translation} secondary={item.descriptor.type} />              
                </ListItem>
              ))}        
            </List>
          </Grid>
          <Grid item>
            <TextField
              id="value-outlined-text"
              label={aggregateByFieldType !== "" ? "Aggregate by / " + aggregateByFieldType : "Aggregate by"}
              size="small"
              select
              fullWidth
              value={aggregateByField}
              onChange={this.handleSelectField}
              variant="outlined"
            >
              {groupByItems.map((item) => (
                  <MenuItem key={item.name} value={item.name}>
                    {item.translation}
                  </MenuItem>
                ))}
            </TextField>
          </Grid>
          <Grid item>
            <TextField
              id="value-outlined-text"
              label="Aggregate op"
              size="small"
              select
              fullWidth
              value={aggregateByFieldOp}
              onChange={this.handleSelectOp}
              variant="outlined"
            >
              {availableOp.map((option) => (
                <MenuItem key={option} value={option}>
                  {option}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item>
          <FormControlLabel
            control={
              <Switch
                checked={this.props.table?.isAggregationFieldInGroupBy ?? false}
                onChange={this.handleAggregationFieldInGroupBy}
                size="small"
              />
            }
            label="Include aggregation field in group by"
          />
        </Grid>
        </Grid>
      </div>
    );
  }
}

export default VisualizeTypeTable;
