  fetchField1 = async (field: EventField) => {
    const fieldType = field.descriptor.type;
    const fieldName = field.name;
    console.log("fetch " + fieldType + " " + fieldName);
    const url1 = this.props.api + "/api/v2/ad-query/field/"+fieldType+"/"+fieldName; //+"?noauth=true";
    const requestOptions: RequestInit = {
      method: 'GET',
      //mode: "no-cors",
      headers: { 
        'Content-Type': 'application/json',
        'Authorization': "Bearer " + this.props.token,
      },
      //body: JSON.stringify({ title: 'React POST Request Example' })
    };

    // let response = await fetch(url1, requestOptions)
    // if (response.ok) return await response.json()
    // throw new Error(response.status)

    return fetch(url1, requestOptions)
      // .then(response => response.json())
      .catch(err => {
        console.log('Error: ' + url1, err)
      })
  };

  async componentDidMount(): Promise<void> {
    console.log("WidgetEditorLoadingPossibleValues componentDidMount")
    const fieldsSet = this.props.eventFields
      .filter((x) => x.descriptor.type === "set");
    const fieldsDictionary = this.props.eventFields
      .filter((x) => x.descriptor.type === "dictionary");
    
    var setValues: Map<string, string[]> = new Map<string, string[]>();
    var dictionaryValues: Map<string, PossibleValueDictionary[]> = new Map<string, PossibleValueDictionary[]>();
    
    let setPromise = Promise.all(fieldsSet.map(async (x) => {
      let response = await this.fetchField1(x);
      //console.log("response="+response);
      if (response && response.ok) {
        let content = await response.json();
        //console.log(x.name + " content="+content.result);
        setValues.set(x.name, content.result);
      } else {
        setValues.set(x.name, []);
      }
      
    }));
    
    let dictionaryPromise = Promise.all(fieldsDictionary.map(async (x) => {
      let response = await this.fetchField1(x);
      //console.log("response="+response);
      if (response && response.ok) {
        let content = await response.json();
        //console.log(x.name + " content="+content.result);
        dictionaryValues.set(x.name, content.result);
      } else {
        dictionaryValues.set(x.name, []);
      }
      
    }));

    console.log("before Promise.all");
    await Promise.all([setPromise, dictionaryPromise]);    
    console.log("after Promise.all");

    console.log("setValues="+JSON.stringify(setValues));
    console.log("dictionaryValues="+JSON.stringify(dictionaryValues));

    const possibleValues: PossibleValues = {
      set: Immutable.fromJS(setValues),
      dictionary: Immutable.fromJS(dictionaryValues),
    };

    this.props.onChangePossibleValues(possibleValues);
     
  }

