import * as React from "react";
import { v4 as uuidv4 } from "uuid";
// import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import WidgetEditorTitle from "./WidgetEditorTitle";
import WidgetEditorLoadingEventFields from "./WidgetEditorLoadingEventFields";
import WidgetEditorLoadingPossibleValues from "./WidgetEditorLoadingPossibleValues";
import WidgetEditorLoadingPossiblePeriods from "./WidgetEditorLoadingPossiblePeriods";
import QueryEditor from "../../components/QueryEditor";
import VisualizeEditor from "../../components/VisualizeEditor";
import WidgetPreviewChart from "../WidgetPreview/WidgetPreviewChart";
import WidgetPreviewPie from "../WidgetPreview/WidgetPreviewPie";
// import WidgetPreviewTable from "../../components/WidgetPreview/WidgetPreviewTable";
import WidgetAggregationTable from "../../components/WidgetPreview/WidgetAggregationTable";
import WidgetEvents from "../../components/WidgetPreview/WidgetEvents";
import WidgetPivotTable from "../../components/WidgetPreview/WidgetPivotTable";
import WidgetCounter from "../WidgetPreview/WidgetCounter";
import WidgetMarkdown from "../../components/WidgetPreview/WidgetMarkdown";
