import Container from "@material-ui/core/Container";
import {
  // AggregationRequest,
  EventField,
  // EventFieldOp,
  ChartState,
  TableState,
  PivotTableState,
  CounterState,
  // QueryAggregationField,
  QueryFilterKey,
  PossibleValues,
  // PossibleValueDictionary,
  TimePeriod,
  Lang,
  WidgetEditorRealState,
  WidgetEditorInternalState,
  WidgetType,
} from "./types";
//import eventFields from "./events-fields.json";
import * as Immutable from "immutable";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";
// import { Typography, Dialog, DialogTitle, DialogContent, DialogActions } from "@material-ui/core";
import WidgetEditorRaw from "./WidgetEditorRaw";

type WidgetEditorProps = {
  dashboardId: string;
  api: string;
  token: string;
  lang: Lang;

  internal: WidgetEditorInternalState;
  onLoadEventFields: (values: EventField[]) => void;
  onLoadPossibleValues: (values: PossibleValues) => void;
  onLoadPossiblePeriods: (values: TimePeriod[]) => void;
  onChangeWidgetEditorState: (widget: WidgetEditorRealState) => void;
  onClose: () => void;
};

type WidgetEditorState = {
  real: WidgetEditorRealState;
  // isHelpOn: boolean;
};

class WidgetEditor extends React.Component<
  WidgetEditorProps,
  WidgetEditorState
> {
  state: WidgetEditorState = {
    real: {
      id: uuidv4(),
      title: "New Widget 1",
      visualizeType: "Chart",

      groupBy: Immutable.List(),
      filterItems: Immutable.List([]),
      period: {
        type: "forever",
      },
    },
    // isHelpOn: false
  };

  handleChangeTitle = (title: string) => {
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        title: title,
      },
    }));
  };

  handleChangeDescription = (description: string) => {
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        description: description,
      },
    }));
  };

  handleChange = (event: any) => {
    console.log(event.target.value);
    const visualizeType: WidgetType = event.target.value;
    // const isCounter = visualizeType === "Counter";
    // const isTable = visualizeType === "Table";
    // const isChart = visualizeType === "Chart" || visualizeType === "Pie";

    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        visualizeType: visualizeType,
        // counter: isCounter ? prevState.real.counter : undefined,
        // chart: isChart ? prevState.real.chart : undefined,
        // table: isTable ? prevState.real.table : undefined,
      },
    }));
  };

  handleTimeperiod = (item: TimePeriod) => {
    console.log("handleTimeperiod " + item.type);
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        period: { type: item.type },
      },
    }));
  };

  handleAddField = (item: EventField) => {
    console.log("handleAddField " + item.name);
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        groupBy: Immutable.List(prevState.real.groupBy).push(item.name),
      },
    }));
  };

  handleRemoveField = (item: EventField) => {
    console.log("handleRemoveField " + item.name);
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        groupBy: prevState.real.groupBy.filter((x) => x !== item.name),
      },
    }));
  };

  handleChartChange = (chart: ChartState) => {
    console.log("handleChartChange " + JSON.stringify(chart));
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        chart: chart,
      },
    }));
  };
  handleTableChange = (table: TableState) => {
    console.log("handleTableChange " + JSON.stringify(table));
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        table: table,
      },
    }));
  };
  handlePivotTableChange = (pivot: PivotTableState) => {
    console.log("handlePivotTableChange " + JSON.stringify(pivot));
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        pivot: pivot,
      },
    }));
  };
  handleCounterChange = (counter: CounterState) => {
    console.log("handleCounterChange " + JSON.stringify(counter));
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        counter: counter,
      },
    }));
  };
  handleMarkdownBottomChange = (bottom: boolean) => {
    console.log("handleMarkdownBottomChange " + bottom);
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        bottom: bottom,
      },
    }));
  };
  handleAddFilterItem = (item: QueryFilterKey) => {
    console.log("handleAddFilterItem " + JSON.stringify(item));
    //    this.setState({ filterItems: this.state.filterItems.push(item) });
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        filterItems: Immutable.List(prevState.real.filterItems).push(item),
      },
    }));
  };
  handleRemoveFilterItem = (item: QueryFilterKey) => {
    console.log("handleRemoveFilterItem " + JSON.stringify(item));
    this.setState((prevState) => ({
      ...prevState,
      real: {
        ...prevState.real,
        filterItems: prevState.real.filterItems.filter(
          (x) => JSON.stringify(x) !== JSON.stringify(item)
        ),
      },
    }));
  };
  // handleLoadEventFields = (values: EventField[]) => {
  //   console.log("handleLoadEventFields " + JSON.stringify(values));
  //   const valuesSorted = values.sort(function (a, b) {
  //     const bandA = a.translation.toUpperCase();
  //     const bandB = b.translation.toUpperCase();

  //     let comparison = 0;
  //     if (bandA > bandB) {
  //       comparison = 1;
  //     } else if (bandA < bandB) {
  //       comparison = -1;
  //     }
  //     return comparison;
  //   });

  //   this.setState((prevState) => ({
  //     ...prevState, internal: {
  //         ...prevState.internal,
  //         groupByAvailableLoaded: true,
  //         groupByAvailable: valuesSorted,
  //       }
  //   }));
  // };
  // handleLoadPossibleValues = (values: PossibleValues) => {
  //   console.log("handleLoadPossibleValues " + JSON.stringify(values));
  //   //this.setState({ possibleValuesLoaded: true, possibleValues: values });
  //   this.setState((prevState) => ({
  //     ...prevState, internal: {
  //         ...prevState.internal,
  //         possibleValuesLoaded: true,
  //         possibleValues: values,
  //       }
  //   }));
  // };
  handleRaw = (raw: string) => {
    const x: WidgetEditorRealState = JSON.parse(raw);
    //TODO:  -   .   
    //this.setState({ title: x.title, description: x.description });
    this.setState((prevState) => ({
      ...prevState,
      real: x,
    }));
  };

  handleEditorDialogSave = () => {
    this.props.onChangeWidgetEditorState(this.state.real);
  };

  // handleHelpModeSwitch = () => {
  //   this.setState((prevState) => ({
  //     ...prevState,
  //     isHelpOn: !prevState.isHelpOn,
  //   }));
  // };

  render() {
    if (!this.props.internal.possiblePeriodsLoaded) {
      return (
        <WidgetEditorLoadingPossiblePeriods
          api={this.props.api}
          token={this.props.token}
          lang={this.props.lang}
          onLoadPossiblePeriods={this.props.onLoadPossiblePeriods}
        />
      );
    }

    if (!this.props.internal.groupByAvailableLoaded) {
      return (
        <WidgetEditorLoadingEventFields
          api={this.props.api}
          token={this.props.token}
          lang={this.props.lang}
          onChangeEventFields={this.props.onLoadEventFields}
        />
      );
    }

    if (!this.props.internal.possibleValuesLoaded) {
      return (
        <WidgetEditorLoadingPossibleValues
          api={this.props.api}
          token={this.props.token}
          eventFields={this.props.internal.groupByAvailable}
          onChangePossibleValues={this.props.onLoadPossibleValues}
        />
      );
    }

    return (
      <div>
        <DialogTitle id="editor-dialog-title">
          <Toolbar variant="dense">
            <Breadcrumbs separator="" aria-label="breadcrumb">
              <Typography color="textPrimary" title="Create title and subtitle for the widget">
                title
              </Typography>
              <Typography color="textPrimary" title="Prepare query by selecting fields and filtering them">
                query
              </Typography>
              <Typography color="textPrimary" title="Visualize the result dataset with number of perdefined widgets">
                visualize
              </Typography>
              <Typography color="textPrimary" title="Preview the result dataset in widget">
                preview
              </Typography>
              <Typography color="textPrimary" title="Save to add widget to current dashboard">
                save
              </Typography>
            </Breadcrumbs>
            <IconButton
              edge="end"
              color="inherit"
              aria-label="help"
              title="All controls have tooltips"
            >            
              <HelpOutlineIcon />
            </IconButton>
          </Toolbar>
        </DialogTitle>
        <DialogContent>
          <Container>
            <Grid container spacing={2} alignItems="stretch">
              <Grid item xs={12}>
                <WidgetEditorTitle
                  title={this.state.real.title}
                  description={this.state.real.description}
                  onChangeTitle={this.handleChangeTitle}
                  onChangeDescription={this.handleChangeDescription}
                />
              </Grid>
              <Grid item xs={12}>
                <WidgetEditorRaw
                  raw={JSON.stringify(this.state.real, undefined, 2)}
                  onChangeRaw={this.handleRaw}
                />
              </Grid>
              {this.state.real.visualizeType !== "Markdown" ? (
                <Grid item xs={12}>
                  <QueryEditor
                    groupBy={this.state.real.groupBy}
                    groupByAvailable={this.props.internal.groupByAvailable}
                    possibleValues={this.props.internal.possibleValues}
                    filterItems={this.state.real.filterItems}
                    period={this.state.real.period}
                    possiblePeriods={this.props.internal.possiblePeriods}
                    onAddField={this.handleAddField}
                    onRemoveField={this.handleRemoveField}
                    onTimeperiod={this.handleTimeperiod}
                    onAddFilterItem={this.handleAddFilterItem}
                    onRemoveFilterItem={this.handleRemoveFilterItem}
                  />
                </Grid>
              ) : null}
              <Grid item xs={12} sm={6}>
                <VisualizeEditor
                  visualizeType={this.state.real.visualizeType}
                  chart={this.state.real.chart}
                  table={this.state.real.table}
                  pivot={this.state.real.pivot}
                  bottom={this.state.real.bottom}
                  counter={this.state.real.counter}
                  groupBy={this.state.real.groupBy}
                  groupByAvailable={this.props.internal.groupByAvailable}
                  possibleValues={this.props.internal.possibleValues}
                  onChange={this.handleChange}
                  onChartChange={this.handleChartChange}
                  onTableChange={this.handleTableChange}
                  onPivotTableChange={this.handlePivotTableChange}
                  onCounterChange={this.handleCounterChange}
                  onMarkdownBottomChange={this.handleMarkdownBottomChange}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                {this.state.real.visualizeType === "Chart" &&
                this.state.real.chart ? (
                  <WidgetPreviewChart
                    isEditMode={false}
                    api={this.props.api}
                    token={this.props.token}
                    id={this.state.real.id}
                    title={this.state.real.title}
                    description={this.state.real.description}
                    chartState={this.state.real.chart!}
                    filterItems={this.state.real.filterItems}
                    period={this.state.real.period}
                    possibleValues={this.props.internal.possibleValues}
                    groupByAvailable={this.props.internal.groupByAvailable}
                  />
                ) : null}
                {this.state.real.visualizeType === "Pie" &&
                this.state.real.chart ? (
                  <WidgetPreviewPie
                    isEditMode={false}
                    api={this.props.api}
                    token={this.props.token}
                    id={this.state.real.id}
                    title={this.state.real.title}
                    description={this.state.real.description}
                    chartState={this.state.real.chart!}
                    filterItems={this.state.real.filterItems}
                    period={this.state.real.period}
                    possibleValues={this.props.internal.possibleValues}
                    groupByAvailable={this.props.internal.groupByAvailable}
                  />
                ) : null}
                {this.state.real.visualizeType === "Table" &&
                this.state.real.table ? (
                  <WidgetAggregationTable
                    isEditMode={false}
                    api={this.props.api}
                    token={this.props.token}
                    id={this.state.real.id}
                    title={this.state.real.title}
                    description={this.state.real.description}
                    tableState={this.state.real.table!}
                    groupBy={this.state.real.groupBy}
                    groupByAvailable={this.props.internal.groupByAvailable}
                    filterItems={this.state.real.filterItems}
                    period={this.state.real.period}
                    possibleValues={this.props.internal.possibleValues}
                  />
                ) : null}
                {this.state.real.visualizeType === "PivotTable" &&
                this.state.real.pivot ? (
                  <WidgetPivotTable
                    isEditMode={false}
                    api={this.props.api}
                    token={this.props.token}
                    id={this.state.real.id}
                    title={this.state.real.title}
                    description={this.state.real.description}
                    tableState={this.state.real.pivot!}
                    filterItems={this.state.real.filterItems}
                    period={this.state.real.period}
                    possibleValues={this.props.internal.possibleValues}
                    groupByAvailable={this.props.internal.groupByAvailable}
                  />
                ) : null}
                {this.state.real.visualizeType === "Events" ? (
                  <WidgetEvents
                    isEditMode={false}
                    api={this.props.api}
                    token={this.props.token}
                    limit={5}
                    id={this.state.real.id}
                    dashboardId={this.props.dashboardId}
                    title={this.state.real.title}
                    description={this.state.real.description}
                    filterItems={this.state.real.filterItems}
                    period={this.state.real.period}
                    possibleValues={this.props.internal.possibleValues}
                  />
                ) : null}
                {this.state.real.visualizeType === "Counter" &&
                this.state.real.counter ? (
                  <WidgetCounter
                    isEditMode={false}
                    api={this.props.api}
                    token={this.props.token}
                    id={this.state.real.id}
                    title={this.state.real.title}
                    description={this.state.real.description}
                    counterState={this.state.real.counter!}
                    filterItems={this.state.real.filterItems}
                    period={this.state.real.period}
                  />
                ) : null}
                {this.state.real.visualizeType === "Markdown" ? (
                  <WidgetMarkdown
                    isEditMode={false}
                    id={this.state.real.id}
                    title={this.state.real.title}
                    description={this.state.real.description}
                  />
                ) : null}
              </Grid>
            </Grid>
          </Container>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onClose} color="primary">
            Cancel
          </Button>
          <Button onClick={this.handleEditorDialogSave} color="primary">
            Save
          </Button>
        </DialogActions>
      </div>
    );
  }
}

export default WidgetEditor;
