import * as React from "react";
import { TimePeriod, Lang } from "./types";
import { Container, Typography, Grid } from "@material-ui/core";
import DashboardIcon from '@material-ui/icons/Dashboard';

type Props = {
  api: string;
  token: string;
  lang: Lang;
  onLoadPossiblePeriods: (values: TimePeriod[]) => void;
};

type State = {
  err?: string;
};

class WidgetEditorLoadingPossiblePeriods extends React.Component<
  Props,
  State
> {
  state: State = {
    err: undefined,
  };
  componentDidMount() {
    // Simple POST request with a JSON body using fetch
    //noauth=true&
    const url1 = this.props.api + "/api/v1/ad-dictionary/timeperiods?lang=" + this.props.lang;
    const requestOptions: RequestInit = {
      method: "GET",
      //mode: "no-cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.props.token,
      },
    };
    fetch(url1, requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.props.onLoadPossiblePeriods(data);
      })
      .catch((e) => {
        this.setState({ err: e.message });
      });
  }

  render() {
    if (this.state.err) {
      return (
        <div>
          <Grid container direction="row" justify="center" alignItems="center" style={{ height: '90vh', margin: 0, padding: 0 }}>
            <Grid item>
              <Container>
                <Typography>Error: {this.state.err}</Typography>
              </Container>
            </Grid>
          </Grid>   
        </div>
      );
    }

    return (
      <div style={{
        height: '90vh', margin: 0, padding: 0,
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
      }}>
        <DashboardIcon/>&nbsp;&nbsp;Loading timeperiods...
      </div>      
    );
  }
}

export default WidgetEditorLoadingPossiblePeriods;
