import * as React from "react";
import { v4 as uuidv4 } from "uuid";
import { NavLink } from "react-router-dom";
// import TextField from "@material-ui/core/TextField";
// import Paper from "@material-ui/core/Paper";
import {
  // Button,
  Dialog,
  DialogTitle,
  // DialogContent,
  // DialogActions,
  IconButton,
  List,
  ListItem,
  ListItemText,
  ListSubheader,
  Divider,
  Tooltip,
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import { WidgetEditorRealState, DashboardState } from "../WidgetEditor/types";

type Props = {
  list: DashboardState[];
  isEditMode: boolean;
  onSelectWidgets: (title: string, widgets: WidgetEditorRealState[]) => void;
};

function DashboardListDialog(props: Props) {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleListItemClick = (
    title: string,
    widgets: WidgetEditorRealState[]
  ) => {
    setOpen(false);

    props.onSelectWidgets(title, widgets);
  };

  return (
    <div>
      <Tooltip title="Create new or select one of the predefined dashboards">
        <IconButton
          edge="start"
          disabled={props.isEditMode}
          color="inherit"
          aria-label="menu"
          onClick={handleClickOpen}
        >
          <MenuIcon />
        </IconButton>
      </Tooltip>
      <Dialog
        onClose={handleClose}
        aria-labelledby="simple-dialog-title"
        maxWidth="xs"
        fullWidth
        open={open}
      >
        <DialogTitle id="simple-dialog-title">Dashboard</DialogTitle>
        <List>
          <ListItem
            button
            component={NavLink}
            to={"/dashboard"}
            onClick={() => handleListItemClick("Dashboard", [])}
            key={uuidv4()}
          >
            <ListItemText primary={"Create new ..."} />
          </ListItem>
          <Divider />
          <ListSubheader>or select</ListSubheader>
          {props.list.map((board) => (
            <ListItem
              button
              component={NavLink}
              to={"/dashboard/" + board.id}
              //to show currently selected use //activeClassName="Mui-selected"
              //onClick={() => handleListItemClick(board.title, board.raw)}
              onClick={handleClose}
              key={board.id}
            >
              <ListItemText primary={board.title} />
            </ListItem>
          ))}
        </List>
      </Dialog>
    </div>
  );
}

export default DashboardListDialog;
