  loadData = () => {
    console.log("WidgetPreviewChart loadData");
    this.setState({ rows: undefined, err: undefined });
    //Simple POST request with a JSON body using fetch
    const url1 = this.props.api + "/api/v2/ad-query/aggregation/table";

    const { x, y, op, series } = this.props.chartState;

    const d = {
      filter: {
        keys: this.props.filterItems
          .filter((xx) => xx.type === "set" || xx.type === "dictionary")
          .map((xx) => {
            return {
              field: xx.field,
              op: xx.op,
              value: xx.value,
            };
          }),
        numbers: this.props.filterItems
          .filter((xx) => xx.type === "number")
          .map((xx) => {
            return {
              field: xx.field,
              op: xx.op,
              value: Number.parseFloat(xx.value),
            };
          }),
        lists: [],
        period: this.props.period,
      },
      groupBy: [x, y, series],
      fields: [
        {
          field: y,
          op: op,
        },
      ],
    };
    const requestOptions: RequestInit = {
      method: "POST",
      //mode: "no-cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.props.token,
      },
      body: JSON.stringify(d),
    };
    fetch(url1, requestOptions)
      //.then(r => r.json().then(json => ({ok: r.ok, status: r.status, json})))
      //.then( r => r.ok ? r.json: Promise.reject(r))
      .then((response) => response.json())
      .then((data) => {
        //this.props.onChangeEventFields(data);
        if (data.result == null) {
          throw Error("no rows in result" + JSON.stringify(data));
        } else {
          const r = data.result;
          // .map((item: any)=>{
          //   return item;
          // });
          console.log("WidgetPreviewLineChart r=" + JSON.stringify(r));
          this.setState({ rows: r });
        }
        console.log("WidgetPreviewLineChart data=" + JSON.stringify(data));
      })
      .catch((e) => {
        this.setState({ err: e.message });
      });
  };
  
