import * as React from "react";
// import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
//import sampleData from "./timeperiods.json";
import { TimePeriod } from "../WidgetEditor/types";

type QueryEditorTimeperiodProps = {
  period: TimePeriod;
  possiblePeriods: TimePeriod[];
  onTimeperiod: (item: TimePeriod) => void;
};

function QueryEditorTimeperiod(props: QueryEditorTimeperiodProps) {
  const timeperiods = props.possiblePeriods;

  const handleTimeperiod = (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleTimeperiod " + event.target.value);
    const item: TimePeriod =
      timeperiods.find((x) => x.type === event.target.value) ?? timeperiods[0];
    console.log("handleSelectSerie item " + item?.type);
    props.onTimeperiod(item);
  };

  return (
    <div className="QueryEditorTimeperiod">
      <TextField
        id="outlined-select-timeperiod"
        size="small"
        select
        label="Timeperiod"
        variant="outlined"
        onChange={handleTimeperiod}
        value={props.period.type}
      >
        {props.possiblePeriods.map((period, index) => (
          <MenuItem key={period.type} value={period.type}>
            {period.translation}
          </MenuItem>
        ))}
      </TextField>
    </div>
  );
}

export default QueryEditorTimeperiod;
