import * as React from "react";
// import Breadcrumbs from "@material-ui/core/Breadcrumbs";
import Typography from "@material-ui/core/Typography";
// import TextField from "@material-ui/core/TextField";
import VisualizeType from "./VisualizeType";
import VisualizeTypeChart from "./VisualizeTypeChart";
import VisualizeTypePie from "./VisualizeTypePie";
import VisualizeTypeCounter from "./VisualizeTypeCounter";
import VisualizeTypeTable from "./VisualizeTypeTable";
import VisualizeTypePivotTable from "./VisualizeTypePivotTable";
import VisualizeTypeEvents from "./VisualizeTypeEvents";
import VisualizeTypeMarkdown from "./VisualizeTypeMarkdown";
import { Paper } from "@material-ui/core";
import * as Immutable from "immutable";
import { EventField, ChartState, TableState, PivotTableState, CounterState, WidgetType, PossibleValues } from "../WidgetEditor/types";

type VisualizeEditorProps = {
  // using `interface` is also ok
  //message: string;
  visualizeType: WidgetType; // like this
  chart?: ChartState;
  table?: TableState;
  pivot?: PivotTableState;
  bottom?: boolean;
  counter?: CounterState;
  groupBy: Immutable.List<string>;
  groupByAvailable: EventField[];
  possibleValues: PossibleValues;
  onChange: (event: any) => void;
  onChartChange: (chart: ChartState) => void;
  onTableChange: (table: TableState) => void;
  onPivotTableChange: (pivot: PivotTableState) => void;
  onCounterChange: (counter: CounterState) => void;
  onMarkdownBottomChange: (bottom: boolean) => void;
};

class VisualizeEditor extends React.Component<VisualizeEditorProps> {
  // state: MyState = {
  //   // optional second annotation for better type inference
  //   visualizeType: "Chart",
  // };

  // handleChange = (event: any) => {
  //   console.log(event.target.value);
  //   this.setState({ visualizeType: event.target.value})
  // };

  render() {
    return (
      <Paper variant="elevation" elevation={0} title="Choose the way in which the result dataset or some static information will be presented">
        <Typography variant="h5">Visualize</Typography>
        <Typography variant="subtitle2">&nbsp;</Typography>
        <VisualizeType
          value={this.props.visualizeType}
          onChange={this.props.onChange}
        />
        <Typography variant="subtitle2">&nbsp;</Typography>
        {this.props.visualizeType === "Chart" ? (
          <VisualizeTypeChart
            chart={this.props.chart}
            groupBy={this.props.groupBy}
            groupByAvailable={this.props.groupByAvailable}
            onChartChange={this.props.onChartChange}
          />
        ) : null}
        {this.props.visualizeType === "Pie" ? (
          <VisualizeTypePie
            chart={this.props.chart}
            groupBy={this.props.groupBy}
            groupByAvailable={this.props.groupByAvailable}
            onChartChange={this.props.onChartChange}
          />
        ) : null}
        {this.props.visualizeType === "Counter" ? (
          <VisualizeTypeCounter 
            counter={this.props.counter}
            groupBy={this.props.groupBy}
            groupByAvailable={this.props.groupByAvailable}
            onCounterChange={this.props.onCounterChange}
          />
        ) : null}
        {this.props.visualizeType === "Table" ? (
          <VisualizeTypeTable
            table={this.props.table}
            groupBy={this.props.groupBy}
            groupByAvailable={this.props.groupByAvailable}
            onTableChange={this.props.onTableChange}
          />
        ) : null}
        {this.props.visualizeType === "PivotTable" ? (
          <VisualizeTypePivotTable
            pivot={this.props.pivot}
            groupBy={this.props.groupBy}
            groupByAvailable={this.props.groupByAvailable}
            possibleValues={this.props.possibleValues}
            onPivotTableChange={this.props.onPivotTableChange}
          />
        ) : null}
        {this.props.visualizeType === "Events" ? (
          <VisualizeTypeEvents />
        ) : null}
        {this.props.visualizeType === "Markdown" ? (
          <VisualizeTypeMarkdown 
            bottom={this.props.bottom}
            onMarkdownBottomChange={this.props.onMarkdownBottomChange}
          />
        ) : null}
        <Typography variant="subtitle2">&nbsp;</Typography>
      </Paper>
    );
  }
}

export default VisualizeEditor;
